import React from "react";
import MainSlider from "./MainSlider";
import ProductTab from "../../ProductTab";
import ProductTabMini from "../../ProductTabMini";
import BannerSection from "../../BannerSection";
import MainBlog from "./MainBlog";
import { Helmet, HelmetProvider } from "react-helmet-async";

const Main = ({
    handleAddProduct,
    cartItems,
    handleAddProductWishlist,
    wishlistItems,
    isModal,
    setIsModal,
    categories,
    isCategoriesLoading,
    categoriesError,
    subCategories,
    isSubCategoriesLoading,
    subCategoriesError,
    news,
    pathname,
    links,
    setProductModal,
    setProductModalId,
}) => {
    return (
        <>
            <HelmetProvider>
                <Helmet>
                    <title> </title>
                    <meta name="description"
                        content='        , ,           " "  .' />
                </Helmet>
            </HelmetProvider>
            <MainSlider />
            <ProductTab
                categories={categories}
                isCategoriesLoading={isCategoriesLoading}
                categoriesError={categoriesError}
                handleAddProduct={handleAddProduct}
                cartItems={cartItems}
                handleAddProductWishlist={handleAddProductWishlist}
                wishlistItems={wishlistItems}
                isModal={isModal}
                setIsModal={setIsModal}
                pathname={pathname}
                links={links}
                setProductModal={setProductModal}
                setProductModalId={setProductModalId}
            />
            <ProductTabMini
                subCategories={subCategories}
                isSubCategoriesLoading={isSubCategoriesLoading}
                subCategoriesError={subCategoriesError}
                handleAddProduct={handleAddProduct}
                cartItems={cartItems}
                handleAddProductWishlist={handleAddProductWishlist}
                wishlistItems={wishlistItems}
                isModal={isModal}
                setIsModal={setIsModal}
                pathname={pathname}
                links={links}
                setProductModal={setProductModal}
                setProductModalId={setProductModalId}
            />
            <BannerSection />
            <MainBlog
                news={news}
            />
        </>
    );
}

export default Main;