import React, { useState, useEffect } from "react";
import ShopItemDescr from "./ShopItemDescr";
import ShopItemReviews from "./ShopItemReviews";
import ShopItemSpecif from "./ShopItemSpecif";
import axios from "axios";
import { useCallback } from "react";


const ShopItemTabs = ({ item }) => {
    const [tabActive, setTabActive] = useState(1);
    const [feedback, setFeedback] = useState([]);
    const [loading, setLoading] = useState(true);

    const getFeedback = useCallback(() => {
        setLoading(true);
        axios.get(`https://deshevle-tut.ru/feedback/?product_id=${item.data[0].ID}`)
            .then(response => setFeedback(response.data.data));
        setLoading(false);
    })

    useEffect(() => {
        getFeedback();
        if (item.data[0].description === "" || item.data[0].description === "0") {
            setTabActive(3);
        } else {
            setTabActive(1);
        }
    }, [])

    useEffect(() => {
        getFeedback();
        if (item.data[0].description === "" || item.data[0].description === "0") {
            setTabActive(3);
        } else {
            setTabActive(1);
        }
    }, [item])

    return (
        <div className="product-details-content-tab-section section-inner-bg section-top-gap-100">
            <div className="container">
                <div className="row">
                    <div className="col-12">
                        <div className="product-details-content-tab-wrapper">
                            <ul className="nav tablist product-details-content-tab-btn d-flex justify-content-center">
                                {item.data[0].description === "" || item.data[0].description === "0" ? '' :
                                    <li><span className={tabActive === 1 ? "nav-link active" : "nav-link"} onClick={() => { setTabActive(1) }}>
                                        <h5></h5>
                                    </span></li>
                                }
                                {item.data[0].features.length === 0 ? '' :
                                    <li><span className={tabActive === 2 ? "nav-link active" : "nav-link"} onClick={() => { setTabActive(2) }}>
                                        <h5></h5>
                                    </span></li>
                                }
                                <li><span className={tabActive === 3 ? "nav-link active" : "nav-link"} onClick={() => { setTabActive(3) }}>
                                    <h5> ({feedback.length})</h5>
                                </span></li>
                            </ul>
                            <div className="product-details-content-tab">
                                <div className="tab-content">
                                    {tabActive === 1 ?
                                        <ShopItemDescr
                                            item={item}
                                        />
                                        :
                                        tabActive === 2 && item.data[0].features.length !== 0 ?
                                            <ShopItemSpecif
                                                item={item}
                                            />
                                            :
                                            <ShopItemReviews
                                                item={item}
                                                feedback={feedback}
                                                loading={loading}
                                                getFeedback={getFeedback}
                                            />
                                    }
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default ShopItemTabs;