import React, { useEffect, useState } from 'react';
import { NavLink } from 'react-router-dom';
import CheckoutFizik from './CheckoutFizik';
import CheckoutJurik from './CheckoutJurik';
import { toast } from "react-hot-toast";
import axios from 'axios';
import ReCAPTCHA from "react-google-recaptcha";
import { Helmet, HelmetProvider } from "react-helmet-async"

const Checkout = ({ cartItems }) => {
    const totalPrice = cartItems.reduce((price, item) => price + item.quantity * item.price, 0)
    const [isFizik, setIsFizik] = useState(true)
    const [checkedCaptcha, setCheckedCaptcha] = useState(false)
    const recaptchaRef = React.createRef();

    const [valuesFizik, setValuesFizik] = useState({
        name: '',
        phone: '',
        email: '',
        note: '',
        partner_cart: 'https://www.google.ru/'
    })

    const [valuesJurik, setValuesJurik] = useState({
        company_name: '',
        inn: '',
        manager_name: '',
        manager_phone: '',
        email: '',
        note: '',
        partner_cart: 'https://www.google.ru/'
    })

    const handleSubmitFizik = async (e) => {
        e.preventDefault();

        let formInfo = {
            form: {},
            products: [],
            user_id: '',
            note: ''
        }

        for (let i = 0; i < cartItems.length; i++) {
            const productInfo = {
                product_id: +cartItems[i]["ID"],
                count: +cartItems[i]["quantity"]
            }
            formInfo.products.push(productInfo);
        }

        const { name, phone, email, partner_cart } = valuesFizik;
        const user = { name, phone, email, partner_cart };

        formInfo.note = valuesFizik.note;
        formInfo.form = user;

        if (checkedCaptcha) {
            if (valuesFizik.name === '' || valuesFizik.phone === '' || valuesFizik.email === '') {
                toast.error(',   !')
            } else {
                try {
                    await axios.post(`https://deshevle-tut.ru/order/`, formInfo)
                        .then(() => { toast.success('   ,       !') })
                        .catch(() => { toast.error('    , ,  ') });
                } catch {
                    toast.error('    ,  !')
                }
            }
        } else {
            toast.error(', ,    !')
        }
    }

    const handleSubmitJurik = async (e) => {
        e.preventDefault();

        let formInfo = {
            form: {},
            products: [],
            user_id: '',
            note: ''
        }

        for (let i = 0; i < cartItems.length; i++) {
            const productInfo = {
                product_id: +cartItems[i]["ID"],
                count: +cartItems[i]["quantity"]
            }
            formInfo.products.push(productInfo);
        }

        const {
            company_name,
            inn,
            manager_name,
            manager_phone,
            email,
            note,
            partner_cart
        } = valuesJurik;
        const user = {
            company_name,
            inn,
            manager_name,
            manager_phone,
            email,
            note,
            partner_cart
        };

        formInfo.note = valuesJurik.note;
        formInfo.form = user

        if (checkedCaptcha) {
            if (valuesJurik.company_name === '' ||
                valuesJurik.inn === '' ||
                valuesJurik.manager_name === '' ||
                valuesJurik.manager_phone === '' ||
                valuesJurik.email === '') {
                toast.error(',   !')
            } else {
                try {
                    await axios.post(`https://deshevle-tut.ru/order/`, formInfo)
                        .then(() => {
                            toast.success('   ,       !')
                        })
                        .catch(() => { toast.error('    , ,  ') });
                } catch {
                    toast.error('    ,  !')
                }
            }
        } else {
            toast.error(', ,    !')
        }
    }

    const handleChangeFizik = name => e => {
        setValuesFizik({ ...valuesFizik, [name]: e.target.value });
    };

    const handleChangeJurik = name => e => {
        setValuesJurik({ ...valuesJurik, [name]: e.target.value });
    };

    return (
        <>
            <HelmetProvider>
                <Helmet>
                    <title>  |  </title>
                    <meta name="description"
                        content='        , ,           " "  .' />
                </Helmet>
            </HelmetProvider>
            {cartItems.length === 0 ?
                <>
                    <div className="breadcrumb-section">
                        <div className="breadcrumb-wrapper">
                            <div className="container">
                                <div className="row">
                                    <div className="col-12 d-flex justify-content-between justify-content-md-between  align-items-center flex-md-row flex-column">
                                        <h3 className="breadcrumb-title"> </h3>
                                        <div className="breadcrumb-nav">
                                            <nav aria-label="breadcrumb">
                                                <ul>
                                                    <li><NavLink to="/"></NavLink></li>
                                                    <li><NavLink to="/shop"></NavLink></li>
                                                    <li className="active" aria-current="page"> </li>
                                                </ul>
                                            </nav>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="checkout_section">
                        <div className="container">
                            <div className="row">
                                <div className="col-12">
                                    <span style={{ fontSize: 20 }}>    ,    :)</span>
                                    <div className="checkout-button">
                                        <NavLink className="hero-button checkout-btn" to="/shop"> </NavLink>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </>
                :
                <>
                    <div className="breadcrumb-section">
                        <div className="breadcrumb-wrapper">
                            <div className="container">
                                <div className="row">
                                    <div className="col-12 d-flex justify-content-between justify-content-md-between  align-items-center flex-md-row flex-column">
                                        <h3 className="breadcrumb-title"> </h3>
                                        <div className="breadcrumb-nav">
                                            <nav aria-label="breadcrumb">
                                                <ul>
                                                    <li><NavLink to="/"></NavLink></li>
                                                    <li><NavLink to="/shop"></NavLink></li>
                                                    <li className="active" aria-current="page"> </li>
                                                </ul>
                                            </nav>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="checkout_section">
                        <div className="container">
                            <div className="checkout_form">

                                <div className="row">
                                    <div className="col-lg-6 col-md-6">
                                        <form>
                                            <h3> </h3>
                                            <div className="row">
                                                <div className="col-lg-6 mb-20">
                                                    <div className={isFizik ? "checkout__tab active" : "checkout__tab"} onClick={() => { setIsFizik(true) }}>
                                                        . 
                                                    </div>
                                                </div>
                                                <div className="col-lg-6 mb-20">
                                                    <div className={isFizik ? "checkout__tab" : "checkout__tab active"} onClick={() => { setIsFizik(false) }}>
                                                        . 
                                                    </div>
                                                </div>
                                            </div>
                                            {isFizik ?
                                                <CheckoutFizik
                                                    handleChangeFizik={handleChangeFizik}
                                                    valuesFizik={valuesFizik}
                                                /> :
                                                <CheckoutJurik
                                                    handleChangeJurik={handleChangeJurik}
                                                    valuesJurik={valuesJurik}
                                                />}
                                        </form>
                                    </div>
                                    <div className="col-lg-6 col-md-6">
                                        <form style={{ position: 'sticky', top: '75px' }} onSubmit={isFizik ? handleSubmitFizik : handleSubmitJurik}>
                                            <h3> </h3>
                                            <div className="order_table table-responsive">
                                                <table>
                                                    <thead>
                                                        <tr>
                                                            <th></th>
                                                            <th></th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        {cartItems.map((item, key) => (
                                                            <tr key={key}>
                                                                <td> {item.title} <strong>  {item.quantity}</strong></td>
                                                                <td> {item.quantity * item.price} .</td>
                                                            </tr>
                                                        ))}
                                                    </tbody>
                                                    <tfoot>
                                                        <tr className="order_total">
                                                            <th></th>
                                                            <td><strong>{totalPrice} .</strong></td>
                                                        </tr>
                                                    </tfoot>
                                                </table>
                                            </div>
                                            <div className="payment_method">
                                                <div className="order_button pt-15">
                                                    <button type="submit"> </button>
                                                </div>
                                                <ReCAPTCHA className="pt-15"
                                                    ref={recaptchaRef}
                                                    sitekey={process.env.REACT_APP_RECAPTCHA_KEY}
                                                    onChange={() => { setCheckedCaptcha(true) }}
                                                />
                                            </div>
                                        </form>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </>
            }
        </>
    );
}

export default Checkout;