import React, { useEffect, useState } from "react";
import { Navigation, Lazy } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import { NavLink } from 'react-router-dom';
import { formatUrl } from '../../../helpers/formatters';

const ShopItemRelated = ({
    item,
    handleAddProduct,
    handleAddProductWishlist,
    scrollToTop,
    setProductModalId,
    setProductModal
}) => {

    return (
        <>
            {item.relatives.length === 0 ? '' : <div className="product-section section-top-gap-100">
                <div className="section-content-gap">
                    <div className="container">
                        <div className="row">
                            <div className="section-content">
                                <h3 className="section-title"> </h3>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="product-wrapper">
                    <div className="container">
                        <div className="row">
                            <div className="col-12">
                                <div className="product-default-slider product-default-slider-4grids-1row">
                                    <Swiper
                                        modules={[Navigation, Lazy]}
                                        spaceBetween={15}
                                        navigation
                                        lazy={true}
                                        autoHeight={true}
                                        breakpoints={{
                                            320: {
                                                slidesPerView: 1
                                            },
                                            576: {
                                                slidesPerView: 2
                                            },
                                            992: {
                                                slidesPerView: 3
                                            },
                                            1200: {
                                                slidesPerView: 4
                                            }
                                        }}>
                                        {item.relatives.map((productItem, key) => (
                                            <SwiperSlide key={key}>
                                                <div className="product-default-single border-around">
                                                    <div className="product-img-warp">
                                                        <NavLink to={`/item/${productItem.ID}`} className="product-default-img-link" onClick={() => { scrollToTop() }}>
                                                            <img className={productItem.image_432 === 'https://deshevle-tut.ru/data/photos/0' || productItem.image_432 === '' || productItem.image_432 === 'https://deshevle-tut.ru/data/photos/432/432__--.jpg' ? 'product-default-img img-fluid none-image' : 'product-default-img img-fluid'} src={productItem.image_432 === 'https://deshevle-tut.ru/data/photos/0' || productItem.image_432 === '' || productItem.image_432 === 'https://deshevle-tut.ru/data/photos/432/432__--.jpg' ? 'https://deshevle-tut.ru/data/photos/432/432__--.jpg' : formatUrl(productItem.image_432)} alt={productItem.title} />
                                                        </NavLink>
                                                        <div className="product-action-icon-link">
                                                            <ul>
                                                                <li>
                                                                    <span onClick={() => { handleAddProductWishlist(productItem); }}>
                                                                        <svg className="svg-icon svg-icon-item" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
                                                                            <path d="M0 190.9V185.1C0 115.2 50.52 55.58 119.4 44.1C164.1 
                                                                36.51 211.4 51.37 244 84.02L256 96L267.1 84.02C300.6 51.37 347 36.51 392.6 
                                                                44.1C461.5 55.58 512 115.2 512 185.1V190.9C512 232.4 494.8 272.1 464.4 300.4L283.7 
                                                                469.1C276.2 476.1 266.3 480 256 480C245.7 480 235.8 476.1 228.3 469.1L47.59 300.4C17.23 
                                                                272.1 .0003 232.4 .0003 190.9L0 190.9z"></path>
                                                                        </svg>
                                                                    </span>
                                                                </li>
                                                                <li>
                                                                    <span onClick={() => { handleAddProduct(productItem); }}>
                                                                        <svg className="svg-icon svg-icon-item" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 576 512">
                                                                            <path d="M96 0C107.5 0 117.4 8.19 119.6 19.51L121.1 32H541.8C562.1 
                                                                32 578.3 52.25 572.6 72.66L518.6 264.7C514.7 278.5 502.1 288 487.8 
                                                                288H170.7L179.9 336H488C501.3 336 512 346.7 512 360C512 373.3 501.3 
                                                                384 488 384H159.1C148.5 384 138.6 375.8 136.4 364.5L76.14 48H24C10.75 
                                                                48 0 37.25 0 24C0 10.75 10.75 0 24 0H96zM128 464C128 437.5 149.5 416 176 
                                                                416C202.5 416 224 437.5 224 464C224 490.5 202.5 512 176 512C149.5 512 128 
                                                                490.5 128 464zM512 464C512 490.5 490.5 512 464 512C437.5 512 416 490.5 416 
                                                                464C416 437.5 437.5 416 464 416C490.5 416 512 437.5 512 464z" />
                                                                        </svg>
                                                                    </span>
                                                                </li>
                                                                <li>
                                                                    <span onClick={() => { setProductModalId(productItem.ID); setProductModal(true) }}>
                                                                        <svg className="svg-icon svg-icon-item" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 576 512">
                                                                            <path d="M279.6 160.4C282.4 160.1 285.2 160 288 160C341 
                                                                            160 384 202.1 384 256C384 309 341 352 288 352C234.1 
                                                                            352 192 309 192 256C192 253.2 192.1 250.4 192.4 247.6C201.7 
                                                                            252.1 212.5 256 224 256C259.3 256 288 227.3 288 192C288 180.5 
                                                                            284.1 169.7 279.6 160.4zM480.6 112.6C527.4 156 558.7 207.1 573.5 
                                                                            243.7C576.8 251.6 576.8 260.4 573.5 268.3C558.7 304 527.4 355.1 
                                                                            480.6 399.4C433.5 443.2 368.8 480 288 480C207.2 480 142.5 443.2 
                                                                            95.42 399.4C48.62 355.1 17.34 304 2.461 268.3C-.8205 260.4-.8205 
                                                                            251.6 2.461 243.7C17.34 207.1 48.62 156 95.42 112.6C142.5 68.84 
                                                                            207.2 32 288 32C368.8 32 433.5 68.84 480.6 112.6V112.6zM288 112C208.5 
                                                                            112 144 176.5 144 256C144 335.5 208.5 400 288 400C367.5 400 432 335.5 
                                                                            432 256C432 176.5 367.5 112 288 112z" /></svg>
                                                                    </span>
                                                                </li>
                                                            </ul>
                                                        </div>
                                                    </div>
                                                    <div className="product-default-content">
                                                        <h6 className="product-default-link"><NavLink to={`/item/${productItem.ID}`} onClick={() => { scrollToTop() }}>{productItem.title}</NavLink></h6>
                                                        <span className="product-default-price">{productItem.price === 0 ? '' : productItem.price + ' .'}</span>
                                                    </div>
                                                </div>
                                            </SwiperSlide>
                                        ))}
                                    </Swiper>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>}
        </>
    );
}

export default ShopItemRelated;