import React, { useState, useEffect } from "react";
import bgkGrid from "../../../images/icon/bkg_grid.png";
import bgkList from "../../../images/icon/bkg_list.png"
import { NavLink } from 'react-router-dom';
import { formatUrl } from '../../../helpers/formatters';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import CartModal from '../Cart/CartModal';
import InfiniteScroll from 'react-infinite-scroll-component';
import Loading from '../../Loading';
import axios from "axios";
import { useCallback } from "react";
import { Helmet, HelmetProvider } from 'react-helmet-async';

const Shop = ({
    handleAddProductShop,
    cartItems,
    isModal,
    setIsModal,
    handleAddProductWishlist,
    categories,
    subCategories,
    setHidden,
    pricelistLinks,
    setProductModalId,
    setProductModal,
    choosedCat
}) => {
    const [isMobile, setIsMobile] = useState(false);
    const [items, setItems] = useState([]);
    const [allItems, setAllItems] = useState([]);
    const [page, setPage] = useState(1);
    const [loading, setLoading] = useState(true);
    const [isCards, setIsCards] = useState(false);
    const [filter, setFilter] = useState(true);
    const [filterModal, setFilterModal] = useState(false);
    const [showFilterModal, setShowFilterModal] = useState(false);
    const [isCheckFilter, setIsCheckFilter] = useState(0);
    const urlParametrs = window.location.search.split('=');

    const [checkedNumsCategories, setCheckedNumsCategories] = useState(urlParametrs[0] === '?spare_parts_unit' ? [] : urlParametrs[1] !== undefined ? urlParametrs[1].split('&')[0].split(',') : []);
    const [checkedInputsCategories, setCheckedInputsCategories] = useState({})

    const [checkedNumsSubCategories, setCheckedNumsSubCategories] = useState(urlParametrs[0] === '?spare_parts_unit' ? urlParametrs[1] !== undefined ? urlParametrs[1].split(',') : [] : urlParametrs[2] !== undefined ? urlParametrs[2].split(',') : []);
    const [checkedInputsSubCategories, setCheckedInputsSubCategories] = useState({})

    const handleResize = () => {
        if (window.innerWidth < 767) {
            setIsMobile(true)
        } else {
            setIsMobile(false)
        }
        if (window.innerWidth < 992) {
            setFilterModal(true)
        } else {
            setFilterModal(false)
        }
    }

    const getItems = useCallback(async () => {
        setLoading(true);
        await axios.get(`https://deshevle-tut.ru/products/?page=1&limit=50`)
            .then(response => {
                setItems(response.data.data)
            })
    })

    const getCheckedItems = useCallback(async (numbersCategories, numbersSubCategories) => {
        setLoading(true);
        if (numbersCategories.length === 0 && numbersSubCategories.length === 0) {
            setPage(2);
            setFilter(true);
            await axios.get(`https://deshevle-tut.ru/products/`)
                .then(response => {
                    setAllItems(response.data.data)
                })
                .then(() => { setLoading(false) });
            await axios.get(`https://deshevle-tut.ru/products/?page=1&limit=50`)
                .then(response => {
                    setItems(response.data.data)
                })
                .then(() => { setLoading(false) })
        } else if (numbersCategories.length !== 0 && numbersSubCategories.length === 0) {
            setPage(2);
            let postNumbersCategories = numbersCategories.join(',')
            setLoading(true);
            await axios.get(`https://deshevle-tut.ru/products/?cat_id=${postNumbersCategories}`)
                .then(response => {
                    response.data.data === null ? setAllItems([]) : setAllItems(response.data.data)
                })
                .then(() => { setLoading(false) })
            await axios.get(`https://deshevle-tut.ru/products/?cat_id=${postNumbersCategories}&page=1&limit=50`)
                .then(response => {
                    response.data.data === null ? setItems([]) : setItems(response.data.data)
                })
                .then(() => { setLoading(false) })
        } else if (numbersCategories.length === 0 && numbersSubCategories.length !== 0) {
            setPage(2);
            let postNumbersSubCategories = numbersSubCategories.join(',')
            setLoading(true);
            await axios.get(`https://deshevle-tut.ru/products/?spare_parts_unit=${postNumbersSubCategories}`)
                .then(response => {
                    response.data.data === null ? setAllItems([]) : setAllItems(response.data.data)
                })
                .then(() => { setLoading(false) })
            await axios.get(`https://deshevle-tut.ru/products/?spare_parts_unit=${postNumbersSubCategories}&page=1&limit=50`)
                .then(response => {
                    response.data.data === null ? setItems([]) : setItems(response.data.data)
                })
                .then(() => { setLoading(false) })
        } else {
            setPage(2);
            let postNumbersCategories = numbersCategories.join(',')
            let postNumbersSubCategories = numbersSubCategories.join(',')
            setLoading(true);
            await axios.get(`https://deshevle-tut.ru/products/?cat_id=${postNumbersCategories}&spare_parts_unit=${postNumbersSubCategories}`)
                .then(response => {
                    response.data.data === null ? setAllItems([]) : setAllItems(response.data.data)
                })
                .then(() => { setLoading(false) })
            await axios.get(`https://deshevle-tut.ru/products/?cat_id=${postNumbersCategories}&spare_parts_unit=${postNumbersSubCategories}&page=1&limit=50`)
                .then(response => {
                    response.data.data === null ? setItems([]) : setItems(response.data.data)
                })
                .then(() => { setLoading(false) })
        }
    })

    useEffect(() => {
        window.addEventListener("resize", handleResize)
    })

    useEffect(() => {
        handleResize();
        getItems();
        getCheckedItems(checkedNumsCategories, checkedNumsSubCategories);
    }, [])

    useEffect(() => {
        if (urlParametrs[1] !== undefined && urlParametrs[2] !== undefined) {
            getCheckedItems(urlParametrs[1].split('&')[0].split(','), urlParametrs[2].split(','));
            for (let i = 1; i < categories.length + 1; i++) {
                checkedInputsCategories[`${i}`] = false
            }
            checkedInputsCategories[`${urlParametrs[1].split('&')[0].split(',')}`] = true

            for (let i = 1; i < subCategories.length + 1; i++) {
                checkedInputsSubCategories[`${i}`] = false
            }
            checkedInputsSubCategories[`${urlParametrs[2].split(',')}`] = true
        } else if (urlParametrs[1] !== undefined && urlParametrs[2] === undefined) {
            if (urlParametrs[0] === '?spare_parts_unit') {
                getCheckedItems(urlParametrs[1].split(','), []);
                for (let i = 1; i < subCategories.length + 1; i++) {
                    checkedInputsSubCategories[`${i}`] = false
                }
                checkedInputsSubCategories[`${urlParametrs[1].split(',')}`] = true
            } else {
                getCheckedItems(urlParametrs[1].split('&')[0].split(','), []);
                for (let i = 1; i < categories.length + 1; i++) {
                    checkedInputsCategories[`${i}`] = false
                }
                checkedInputsCategories[`${urlParametrs[1].split('&')[0].split(',')}`] = true
            }
        } else if (urlParametrs[1] === undefined && urlParametrs[2] !== undefined) {
            getCheckedItems([], urlParametrs[2].split(','));
            for (let i = 1; i < subCategories.length + 1; i++) {
                checkedInputsSubCategories[`${i}`] = false
            }
            checkedInputsSubCategories[`${urlParametrs[2].split(',')}`] = true
        } else {
            getCheckedItems([], []);
        }
    }, [choosedCat])

    useEffect(() => {
        for (let i = 1; i < categories.length + 1; i++) {
            checkedInputsCategories[`${i}`] = false
        }
        checkedNumsCategories.forEach(el => checkedInputsCategories[`${el}`] = true)

        for (let i = 1; i < subCategories.length + 1; i++) {
            checkedInputsSubCategories[`${i}`] = false
        }
        checkedNumsSubCategories.forEach(el => checkedInputsSubCategories[`${el}`] = true)

    }, [categories, subCategories])

    useEffect(() => {
        getCheckedItems(checkedNumsCategories, checkedNumsSubCategories);
    }, [isCheckFilter])


    const fetchItems = async () => {
        if (checkedNumsCategories.length === 0 && checkedNumsSubCategories.length === 0) {
            const res = axios.get(`https://deshevle-tut.ru/products/?page=${page}&limit=50`);
            const data = await res;
            return data;
        } else if (checkedNumsCategories.length !== 0 && checkedNumsSubCategories.length === 0) {
            let postNumbersCategories = checkedNumsCategories.join(',')
            const res = axios.get(`https://deshevle-tut.ru/products/?cat_id=${postNumbersCategories}&page=${page}&limit=50`);
            const data = await res;
            return data;
        } else if (checkedNumsCategories.length === 0 && checkedNumsSubCategories.length !== 0) {
            let postNumbersSubCategories = checkedNumsSubCategories.join(',')
            const res = axios.get(`https://deshevle-tut.ru/products/?spare_parts_unit=${postNumbersSubCategories}&page=${page}&limit=50`);
            const data = await res;
            return data;
        } else if (checkedNumsCategories.length !== 0 && checkedNumsSubCategories.length !== 0) {
            let postNumbersCategories = checkedNumsCategories.join(',')
            let postNumbersSubCategories = checkedNumsSubCategories.join(',')
            const res = axios.get(`https://deshevle-tut.ru/products/?cat_id=${postNumbersCategories}&spare_parts_unit=${postNumbersSubCategories}&page=${page}&limit=50`);
            const data = await res;
            return data;
        }
    }

    const fetchData = async () => {
        const itemsFromServer = await fetchItems();

        setItems([...items, ...itemsFromServer.data.data]);

        if (itemsFromServer.data.data.length === 0 || itemsFromServer.data.data.length < 50) {
            setFilter(false);
        }

        setPage(page + 1)
    }

    return (
        <>
            <HelmetProvider>
                <Helmet>
                    <title>  | </title>
                    <meta name="description"
                        content='     , . . ,  ,  , .     , , . ...  .    , ,    .' />
                </Helmet>
            </HelmetProvider>
            {loading ? <Loading /> :
                <>
                    <div className="breadcrumb-section">
                        <div className="breadcrumb-wrapper">
                            <div className="container">
                                <div className="row">
                                    <div className="col-12 d-flex justify-content-between justify-content-md-between  align-items-center flex-md-row flex-column">
                                        <h3 className="breadcrumb-title"></h3>
                                        <div className="breadcrumb-nav">
                                            <nav aria-label="breadcrumb">
                                                <ul>
                                                    <li><NavLink to="/"></NavLink></li>
                                                    <li className="active"></li>
                                                </ul>
                                            </nav>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="shop-section">
                        <div className="container">
                            <div className="row flex-lg-row">
                                <div className="col-lg-2 col-12">
                                    {filterModal ?
                                        <div className={showFilterModal ? "modal filter-modal open" : "modal filter-modal"}>
                                            <div className="modal-body modal-body-filter">
                                                <div className="modal-close-block">
                                                    <button className="modal-close close" onClick={() => { setShowFilterModal(false); setHidden(false); }}>
                                                        <span aria-hidden="true"> <i className="fa fa-times"></i></span>
                                                    </button>
                                                </div>
                                                <div className="siderbar-section siderbar-section-mob">
                                                    <div className="sidebar-single-widget">
                                                        <h6 className="sidebar-title"></h6>
                                                        <div className="sidebar-content">
                                                            <div className="filter-type-select">
                                                                <ul>
                                                                    {categories.map((item, key) => (
                                                                        <li key={key}>
                                                                            <label className="checkbox-default" htmlFor={`catagory_${item.ID}`}>
                                                                                <input
                                                                                    type="checkbox"
                                                                                    name="category-filter"
                                                                                    id={`catagory_${item.ID}`}
                                                                                    onChange={(e) => {
                                                                                        if (e.target.checked) {
                                                                                            checkedInputsCategories[`${item.ID}`] = true;
                                                                                            checkedNumsCategories.push(`${item.ID}`);
                                                                                            if (checkedNumsCategories.length !== 0 && checkedNumsSubCategories.length !== 0) {
                                                                                                window.history.replaceState(null, "ShopFilter", `/shop?cat_id=${checkedNumsCategories}&spare_parts_unit=${checkedNumsSubCategories}`)
                                                                                            } else if (checkedNumsCategories.length !== 0 && checkedNumsSubCategories.length === 0) {
                                                                                                window.history.replaceState(null, "ShopFilter", `/shop?cat_id=${checkedNumsCategories}`)
                                                                                            } else if (checkedNumsCategories.length === 0 && checkedNumsSubCategories.length !== 0) {
                                                                                                window.history.replaceState(null, "ShopFilter", `/shop?spare_parts_unit=${checkedNumsSubCategories}`)
                                                                                            }
                                                                                            setIsCheckFilter(e => e + 1);
                                                                                        } else {
                                                                                            let arrCategories = checkedNumsCategories.filter(number => number !== `${item.ID}`);
                                                                                            checkedInputsCategories[`${item.ID}`] = false;
                                                                                            setCheckedNumsCategories(arrCategories);
                                                                                            if (arrCategories.length === 0 && checkedNumsSubCategories.length === 0) {
                                                                                                window.history.replaceState(null, "ShopFilter", `/shop`)
                                                                                            } else {
                                                                                                if (arrCategories.length !== 0 && checkedNumsSubCategories.length !== 0) {
                                                                                                    window.history.replaceState(null, "ShopFilter", `/shop?cat_id=${checkedNumsCategories.filter(number => number !== `${item.ID}`)}&spare_parts_unit=${urlParametrs[2]}`)
                                                                                                } else if (arrCategories.length !== 0 && checkedNumsSubCategories.length === 0) {
                                                                                                    window.history.replaceState(null, "ShopFilter", `/shop?cat_id=${checkedNumsCategories.filter(number => number !== `${item.ID}`)}`)
                                                                                                } else if (arrCategories.length === 0 && checkedNumsSubCategories.length !== 0) {
                                                                                                    window.history.replaceState(null, "ShopFilter", `/shop?spare_parts_unit=${urlParametrs[2]}`)
                                                                                                }
                                                                                            }
                                                                                            setIsCheckFilter(e => e - 1);
                                                                                        }
                                                                                    }}
                                                                                    checked={checkedInputsCategories[`${item.ID}`] === true ? true : false}
                                                                                />
                                                                                <span>{item.title}</span>
                                                                            </label>
                                                                        </li>
                                                                    ))}
                                                                </ul>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div className="sidebar-single-widget">
                                                        <h6 className="sidebar-title"></h6>
                                                        <div className="sidebar-content">
                                                            <div className="filter-type-select">
                                                                <ul>
                                                                    {subCategories.map((item, key) => (
                                                                        <li key={key}>
                                                                            <label className="checkbox-default" htmlFor={`subcatagory_${item.ID}`}>
                                                                                <input
                                                                                    type="checkbox"
                                                                                    name="category-filter"
                                                                                    id={`subcatagory_${item.ID}`}
                                                                                    onChange={(e) => {
                                                                                        if (e.target.checked) {
                                                                                            checkedInputsSubCategories[`${item.ID}`] = true;
                                                                                            checkedNumsSubCategories.push(`${item.ID}`);
                                                                                            if (checkedNumsCategories.length !== 0 && checkedNumsSubCategories.length !== 0) {
                                                                                                window.history.replaceState(null, "ShopFilter", `/shop?cat_id=${checkedNumsCategories}&spare_parts_unit=${checkedNumsSubCategories}`)
                                                                                            } else if (checkedNumsCategories.length !== 0 && checkedNumsSubCategories.length === 0) {
                                                                                                window.history.replaceState(null, "ShopFilter", `/shop?cat_id=${checkedNumsCategories}`)
                                                                                            } else if (checkedNumsCategories.length === 0 && checkedNumsSubCategories.length !== 0) {
                                                                                                window.history.replaceState(null, "ShopFilter", `/shop?spare_parts_unit=${checkedNumsSubCategories}`)
                                                                                            }
                                                                                            setIsCheckFilter(e => e + 1);
                                                                                        } else {
                                                                                            let arrSubCategories = checkedNumsSubCategories.filter(number => number !== `${item.ID}`);
                                                                                            checkedInputsSubCategories[`${item.ID}`] = false;
                                                                                            setCheckedNumsSubCategories(arrSubCategories)
                                                                                            if (checkedNumsCategories.length === 0 && arrSubCategories.length === 0) {
                                                                                                window.history.replaceState(null, "ShopFilter", `/shop`)
                                                                                            } else {
                                                                                                if (checkedNumsCategories.length !== 0 && arrSubCategories.length !== 0) {
                                                                                                    window.history.replaceState(null, "ShopFilter", `/shop?cat_id=${urlParametrs[1].split('&')[0]}&spare_parts_unit=${checkedNumsSubCategories.filter(number => number !== `${item.ID}`)}`)
                                                                                                } else if (checkedNumsCategories.length !== 0 && arrSubCategories.length === 0) {
                                                                                                    window.history.replaceState(null, "ShopFilter", `/shop?cat_id=${urlParametrs[1].split('&')[0]}`)
                                                                                                } else if (checkedNumsCategories.length === 0 && arrSubCategories.length !== 0) {
                                                                                                    window.history.replaceState(null, "ShopFilter", `/shop?spare_parts_unit=${checkedNumsSubCategories.filter(number => number !== `${item.ID}`)}`)
                                                                                                }
                                                                                            }
                                                                                            setIsCheckFilter(e => e - 1);
                                                                                        }
                                                                                    }}
                                                                                    checked={checkedInputsSubCategories[`${item.ID}`] === true ? true : false}
                                                                                />
                                                                                <span>{item.title}</span>
                                                                            </label>
                                                                        </li>
                                                                    ))}
                                                                </ul>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div className="sidebar-single-widget">
                                                        <div className="sidebar-content">
                                                            <span className="sidebar-banner">
                                                                <a href={pricelistLinks.price_1} target="_blank" rel="nofollow noopener" className="sidebar-banner-link">
                                                                    <span>-</span>
                                                                    <span></span>
                                                                </a>
                                                                <a href={pricelistLinks.price_2} target="_blank" rel="nofollow noopener" className="sidebar-banner-link mt-30">
                                                                    <span>-</span>
                                                                    <span></span>
                                                                </a>
                                                            </span>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        :
                                        <div className="siderbar-section">
                                            <div className="sidebar-single-widget">
                                                <h6 className="sidebar-title"></h6>
                                                <div className="sidebar-content">
                                                    <div className="filter-type-select">
                                                        <ul>
                                                            {categories.map((item, key) => (
                                                                <li key={key}>
                                                                    <label className="checkbox-default" htmlFor={`catagory_${item.ID}`}>
                                                                        <input
                                                                            type="checkbox"
                                                                            name="category-filter"
                                                                            id={`catagory_${item.ID}`}
                                                                            onChange={(e) => {
                                                                                if (e.target.checked) {
                                                                                    checkedInputsCategories[`${item.ID}`] = true;
                                                                                    checkedNumsCategories.push(`${item.ID}`);
                                                                                    if (checkedNumsCategories.length !== 0 && checkedNumsSubCategories.length !== 0) {
                                                                                        window.history.replaceState(null, "ShopFilter", `/shop?cat_id=${checkedNumsCategories}&spare_parts_unit=${checkedNumsSubCategories}`)
                                                                                    } else if (checkedNumsCategories.length !== 0 && checkedNumsSubCategories.length === 0) {
                                                                                        window.history.replaceState(null, "ShopFilter", `/shop?cat_id=${checkedNumsCategories}`)
                                                                                    } else if (checkedNumsCategories.length === 0 && checkedNumsSubCategories.length !== 0) {
                                                                                        window.history.replaceState(null, "ShopFilter", `/shop?spare_parts_unit=${checkedNumsSubCategories}`)
                                                                                    }
                                                                                    setIsCheckFilter(e => e + 1);
                                                                                } else {
                                                                                    let arrCategories = checkedNumsCategories.filter(number => number !== `${item.ID}`);
                                                                                    checkedInputsCategories[`${item.ID}`] = false;
                                                                                    setCheckedNumsCategories(arrCategories);
                                                                                    if (arrCategories.length === 0 && checkedNumsSubCategories.length === 0) {
                                                                                        window.history.replaceState(null, "ShopFilter", `/shop`)
                                                                                    } else {
                                                                                        if (arrCategories.length !== 0 && checkedNumsSubCategories.length !== 0) {
                                                                                            window.history.replaceState(null, "ShopFilter", `/shop?cat_id=${checkedNumsCategories.filter(number => number !== `${item.ID}`)}&spare_parts_unit=${urlParametrs[2]}`)
                                                                                        } else if (arrCategories.length !== 0 && checkedNumsSubCategories.length === 0) {
                                                                                            window.history.replaceState(null, "ShopFilter", `/shop?cat_id=${checkedNumsCategories.filter(number => number !== `${item.ID}`)}`)
                                                                                        } else if (arrCategories.length === 0 && checkedNumsSubCategories.length !== 0) {
                                                                                            window.history.replaceState(null, "ShopFilter", `/shop?spare_parts_unit=${urlParametrs[2]}`)
                                                                                        }
                                                                                    }
                                                                                    setIsCheckFilter(e => e - 1);
                                                                                }
                                                                            }}
                                                                            checked={checkedInputsCategories[`${item.ID}`] === true ? true : false}
                                                                        />
                                                                        <span>{item.title}</span>
                                                                    </label>
                                                                </li>
                                                            ))}
                                                        </ul>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="sidebar-single-widget">
                                                <h6 className="sidebar-title"></h6>
                                                <div className="sidebar-content">
                                                    <div className="filter-type-select">
                                                        <ul>
                                                            {subCategories.map((item, key) => (
                                                                <li key={key}>
                                                                    <label className="checkbox-default" htmlFor={`subcatagory_${item.ID}`}>
                                                                        <input
                                                                            type="checkbox"
                                                                            name="category-filter"
                                                                            id={`subcatagory_${item.ID}`}
                                                                            onChange={(e) => {
                                                                                if (e.target.checked) {
                                                                                    checkedInputsSubCategories[`${item.ID}`] = true;
                                                                                    checkedNumsSubCategories.push(`${item.ID}`);
                                                                                    if (checkedNumsCategories.length !== 0 && checkedNumsSubCategories.length !== 0) {
                                                                                        window.history.replaceState(null, "ShopFilter", `/shop?cat_id=${checkedNumsCategories}&spare_parts_unit=${checkedNumsSubCategories}`)
                                                                                    } else if (checkedNumsCategories.length !== 0 && checkedNumsSubCategories.length === 0) {
                                                                                        window.history.replaceState(null, "ShopFilter", `/shop?cat_id=${checkedNumsCategories}`)
                                                                                    } else if (checkedNumsCategories.length === 0 && checkedNumsSubCategories.length !== 0) {
                                                                                        window.history.replaceState(null, "ShopFilter", `/shop?spare_parts_unit=${checkedNumsSubCategories}`)
                                                                                    }
                                                                                    setIsCheckFilter(e => e + 1);
                                                                                } else {
                                                                                    let arrSubCategories = checkedNumsSubCategories.filter(number => number !== `${item.ID}`);
                                                                                    checkedInputsSubCategories[`${item.ID}`] = false;
                                                                                    setCheckedNumsSubCategories(arrSubCategories)
                                                                                    if (checkedNumsCategories.length === 0 && arrSubCategories.length === 0) {
                                                                                        window.history.replaceState(null, "ShopFilter", `/shop`)
                                                                                    } else {
                                                                                        if (checkedNumsCategories.length !== 0 && arrSubCategories.length !== 0) {
                                                                                            window.history.replaceState(null, "ShopFilter", `/shop?cat_id=${urlParametrs[1].split('&')[0]}&spare_parts_unit=${checkedNumsSubCategories.filter(number => number !== `${item.ID}`)}`)
                                                                                        } else if (checkedNumsCategories.length !== 0 && arrSubCategories.length === 0) {
                                                                                            window.history.replaceState(null, "ShopFilter", `/shop?cat_id=${urlParametrs[1].split('&')[0]}`)
                                                                                        } else if (checkedNumsCategories.length === 0 && arrSubCategories.length !== 0) {
                                                                                            window.history.replaceState(null, "ShopFilter", `/shop?spare_parts_unit=${checkedNumsSubCategories.filter(number => number !== `${item.ID}`)}`)
                                                                                        }
                                                                                    }
                                                                                    setIsCheckFilter(e => e - 1);
                                                                                }
                                                                            }}
                                                                            checked={checkedInputsSubCategories[`${item.ID}`] === true ? true : false}
                                                                        />
                                                                        <span>{item.title}</span>
                                                                    </label>
                                                                </li>
                                                            ))}
                                                        </ul>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="sidebar-single-widget">
                                                <div className="sidebar-content">
                                                    <span className="sidebar-banner">
                                                        <a href={pricelistLinks.price_1} target="_blank" rel="nofollow noopener" className="sidebar-banner-link">
                                                            <span>-</span>
                                                            <span></span>
                                                        </a>
                                                        <a href={pricelistLinks.price_2} target="_blank" rel="nofollow noopener" className="sidebar-banner-link mt-30">
                                                            <span>-</span>
                                                            <span></span>
                                                        </a>
                                                    </span>
                                                </div>
                                            </div>

                                        </div>
                                    }

                                </div>
                                <div className="col-lg-10 col-12">
                                    <div className="shop-sort-section">
                                        <div className="container">
                                            <div className="row">
                                                <div className="sort-box d-flex justify-content-between align-items-center flex-row">
                                                    {isMobile ? '' : <div className="sort-tablist">
                                                        <ul className="tablist nav sort-tab-btn">
                                                            <li><span className={isCards ? "nav-link" : "nav-link active"} onClick={() => { setIsCards(false) }}><img src={bgkList} alt="bgk_list" /></span></li>
                                                            <li><span className={isCards ? "nav-link active" : "nav-link"} onClick={() => { setIsCards(true) }}><img src={bgkGrid} alt="bgk_grid" /></span></li>
                                                        </ul>
                                                    </div>}
                                                    {filterModal ? <div className="hero-button filters-button" onClick={() => { setShowFilterModal(true); setHidden(true) }}> </div> : ''}
                                                    {/*<div className="sort-select-list">
                                                        <form action="#">
                                                            <fieldset>
                                                                <select name="speed" id="speed">
                                                                    <option selected>  </option>
                                                                    <option>  :    </option>
                                                                    <option>  :    </option>
                                                                    <option>  : (-)</option>
                                                                </select>
                                                            </fieldset>
                                                        </form>
                                                    </div>*/}
                                                    <div className="page-amount">
                                                        <span> {allItems.length} </span>
                                                    </div>

                                                </div>
                                            </div>
                                        </div>
                                    </div>

                                    <div className="sort-product-tab-wrapper">
                                        <div className="container">
                                            <div className="row">
                                                <div className="col-12">
                                                    <div className="tab-content tab-animate-zoom">
                                                        <div className="tab-pane active show sort-layout-single">
                                                            <div className="row">
                                                                <div className="col-12">
                                                                    <div className="table_desc">
                                                                        <div className="table_page table-responsive">
                                                                            {allItems.length === 0 ?
                                                                                <span style={{ fontSize: '24px' }}>  </span>
                                                                                :
                                                                                loading ? <Loading /> :
                                                                                    isMobile || isCards
                                                                                        ?
                                                                                        <InfiniteScroll
                                                                                            dataLength={items.length}
                                                                                            next={fetchData}
                                                                                            hasMore={filter}
                                                                                            loader={<h4 style={{ textAlign: 'center', marginTop: 15, fontSize: 20, width: '100%' }}>...</h4>}
                                                                                            endMessage={
                                                                                                <p style={{ textAlign: 'center', paddingBottom: '10px', fontSize: 24, width: '100%' }}>
                                                                                                    <b>    !</b>
                                                                                                </p>
                                                                                            }
                                                                                        >
                                                                                            {items.map((item, key) => (
                                                                                                <div className="shop-product product-default-single border-around" key={key}>
                                                                                                    <div className="product-img-warp">
                                                                                                        <NavLink to={`/item/${item.ID}`} className="product-default-img-link">
                                                                                                            <img className={item.image_432 === 'https://deshevle-tut.ru/data/photos/0' || item.image_432 === '' || item.image_432 === 'https://deshevle-tut.ru/data/photos/432/432__--.jpg' ? 'product-default-img img-fluid none-image' : 'product-default-img img-fluid'} src={item.image_432 === 'https://deshevle-tut.ru/data/photos/0' || item.image_432 === '' || item.image_432 === 'https://deshevle-tut.ru/data/photos/432/432__--.jpg' ? 'https://deshevle-tut.ru/data/photos/432/432__--.jpg' : formatUrl(item.image_432)} alt={item.title} />
                                                                                                        </NavLink>
                                                                                                    </div>
                                                                                                    <div className="product-default-content">
                                                                                                        <h6 className="product-default-link"><NavLink to={`/item/${item.ID}`} >{item.title} (. {item.vendor_code})</NavLink></h6>
                                                                                                        <span className="product-default-price">{item.price === 0 ? '' : item.price + ' .'}</span>
                                                                                                        <div className="product_quantity shop_quantity">
                                                                                                            <span>: </span>
                                                                                                            <input className="product-count" type="number" min="1" max="100000" placeholder="1"
                                                                                                                onChange={(e) => {
                                                                                                                    item.quantity = e.target.value;
                                                                                                                }}
                                                                                                            /></div>
                                                                                                        <div className="product-action-icon-link product-action-icon-link-relative">
                                                                                                            <ul>
                                                                                                                <li>
                                                                                                                    <span onClick={() => { handleAddProductWishlist(item); }}>
                                                                                                                        <svg className="svg-icon svg-icon-item" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
                                                                                                                            <path d="M0 190.9V185.1C0 115.2 50.52 55.58 119.4 44.1C164.1 
                                                                                                                        36.51 211.4 51.37 244 84.02L256 96L267.1 84.02C300.6 51.37 347 36.51 392.6 
                                                                                                                        44.1C461.5 55.58 512 115.2 512 185.1V190.9C512 232.4 494.8 272.1 464.4 300.4L283.7 
                                                                                                                        469.1C276.2 476.1 266.3 480 256 480C245.7 480 235.8 476.1 228.3 469.1L47.59 300.4C17.23 
                                                                                                                        272.1 .0003 232.4 .0003 190.9L0 190.9z"></path>
                                                                                                                        </svg>
                                                                                                                    </span>
                                                                                                                </li>
                                                                                                                <li>
                                                                                                                    <span onClick={() => { handleAddProductShop(item); }}>
                                                                                                                        <svg className="svg-icon svg-icon-item" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 576 512">
                                                                                                                            <path d="M96 0C107.5 0 117.4 8.19 119.6 19.51L121.1 32H541.8C562.1 
                                                                                                                        32 578.3 52.25 572.6 72.66L518.6 264.7C514.7 278.5 502.1 288 487.8 
                                                                                                                        288H170.7L179.9 336H488C501.3 336 512 346.7 512 360C512 373.3 501.3 
                                                                                                                        384 488 384H159.1C148.5 384 138.6 375.8 136.4 364.5L76.14 48H24C10.75 
                                                                                                                        48 0 37.25 0 24C0 10.75 10.75 0 24 0H96zM128 464C128 437.5 149.5 416 176 
                                                                                                                        416C202.5 416 224 437.5 224 464C224 490.5 202.5 512 176 512C149.5 512 128 
                                                                                                                        490.5 128 464zM512 464C512 490.5 490.5 512 464 512C437.5 512 416 490.5 416 
                                                                                                                        464C416 437.5 437.5 416 464 416C490.5 416 512 437.5 512 464z" />
                                                                                                                        </svg>
                                                                                                                    </span>
                                                                                                                </li>
                                                                                                                <li>
                                                                                                                    <span onClick={() => { setProductModalId(item.ID); setProductModal(true) }}>
                                                                                                                        <svg className="svg-icon svg-icon-item" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 576 512">
                                                                                                                            <path d="M279.6 160.4C282.4 160.1 285.2 160 288 160C341 
                                                                                                                        160 384 202.1 384 256C384 309 341 352 288 352C234.1 
                                                                                                                        352 192 309 192 256C192 253.2 192.1 250.4 192.4 247.6C201.7 
                                                                                                                        252.1 212.5 256 224 256C259.3 256 288 227.3 288 192C288 180.5 
                                                                                                                        284.1 169.7 279.6 160.4zM480.6 112.6C527.4 156 558.7 207.1 573.5 
                                                                                                                        243.7C576.8 251.6 576.8 260.4 573.5 268.3C558.7 304 527.4 355.1 
                                                                                                                        480.6 399.4C433.5 443.2 368.8 480 288 480C207.2 480 142.5 443.2 
                                                                                                                        95.42 399.4C48.62 355.1 17.34 304 2.461 268.3C-.8205 260.4-.8205 
                                                                                                                        251.6 2.461 243.7C17.34 207.1 48.62 156 95.42 112.6C142.5 68.84 
                                                                                                                        207.2 32 288 32C368.8 32 433.5 68.84 480.6 112.6V112.6zM288 112C208.5 
                                                                                                                        112 144 176.5 144 256C144 335.5 208.5 400 288 400C367.5 400 432 335.5 
                                                                                                                        432 256C432 176.5 367.5 112 288 112z" /></svg>
                                                                                                                    </span>
                                                                                                                </li>
                                                                                                            </ul>
                                                                                                        </div>
                                                                                                    </div>
                                                                                                </div>
                                                                                            ))}
                                                                                        </InfiniteScroll>
                                                                                        :
                                                                                        <InfiniteScroll
                                                                                            dataLength={items.length}
                                                                                            next={fetchData}
                                                                                            hasMore={filter}
                                                                                            loader={<h4 style={{ textAlign: 'center', marginTop: 15, marginBottom: 15, fontSize: 20, width: '100%' }}>...</h4>}
                                                                                            endMessage={
                                                                                                <p style={{ textAlign: 'center', marginTop: 15, fontSize: 20, width: '100%' }}>
                                                                                                    <b>    !</b>
                                                                                                </p>
                                                                                            }
                                                                                        >
                                                                                            <table>
                                                                                                <thead>
                                                                                                    <tr>
                                                                                                        <th className="product_thumb"></th>
                                                                                                        <th className="product_name"></th>
                                                                                                        <th className="product_articule"></th>
                                                                                                        <th className="product-price"></th>
                                                                                                        <th className="product_quantity"></th>
                                                                                                        <th className="product_addcart"></th>
                                                                                                    </tr>
                                                                                                </thead>
                                                                                                <tbody>

                                                                                                    {items.map((item, key) => (
                                                                                                        <tr key={key}>
                                                                                                            <td className="product_thumb"><NavLink to={`/item/${item.ID}`}>
                                                                                                                <LazyLoadImage
                                                                                                                    className={item.image_432 === 'https://deshevle-tut.ru/data/photos/0' || item.image_432 === '' || item.image_432 === 'https://deshevle-tut.ru/data/photos/432/432__--.jpg' ? 'none-image' : ''}
                                                                                                                    src={item.image_432 === 'https://deshevle-tut.ru/data/photos/0' || item.image_128 === '' || item.image_128 === 'https://deshevle-tut.ru/data/photos/128/128__--.jpg' ? 'https://deshevle-tut.ru/data/photos/128/128__--.jpg' : formatUrl(item.image_128)}
                                                                                                                    effect="blur"
                                                                                                                    alt={item.title}
                                                                                                                /></NavLink>
                                                                                                            </td>
                                                                                                            <td className="product_name"><NavLink to={`/item/${item.ID}`}>{item.title}</NavLink></td>
                                                                                                            <td className="product_articule"><span>{item.vendor_code}</span></td>
                                                                                                            <td className="product-price">{item.price === 0 ? '' : item.price + ' .'}</td>
                                                                                                            <td className="product_quantity">
                                                                                                                <input className="product-count" type="number" min="1" max="100000" placeholder="1"
                                                                                                                    onChange={(e) => {
                                                                                                                        item.quantity = e.target.value;
                                                                                                                    }}
                                                                                                                />
                                                                                                            </td>
                                                                                                            <td className="product_add">
                                                                                                                <div className="product-action-icon-link product-action-icon-link-relative product-action-icon-link-relative-table">
                                                                                                                    <ul>
                                                                                                                        <li>
                                                                                                                            <span onClick={() => { handleAddProductWishlist(item); }}>
                                                                                                                                <svg className="svg-icon svg-icon-item" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
                                                                                                                                    <path d="M0 190.9V185.1C0 115.2 50.52 55.58 119.4 44.1C164.1 
                                                                                                                                36.51 211.4 51.37 244 84.02L256 96L267.1 84.02C300.6 51.37 347 36.51 392.6 
                                                                                                                                44.1C461.5 55.58 512 115.2 512 185.1V190.9C512 232.4 494.8 272.1 464.4 300.4L283.7 
                                                                                                                                469.1C276.2 476.1 266.3 480 256 480C245.7 480 235.8 476.1 228.3 469.1L47.59 300.4C17.23 
                                                                                                                                272.1 .0003 232.4 .0003 190.9L0 190.9z"></path>
                                                                                                                                </svg>
                                                                                                                            </span>
                                                                                                                        </li>
                                                                                                                        <li>
                                                                                                                            <span onClick={() => { handleAddProductShop(item); }}>
                                                                                                                                <svg className="svg-icon svg-icon-item" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 576 512">
                                                                                                                                    <path d="M96 0C107.5 0 117.4 8.19 119.6 19.51L121.1 32H541.8C562.1 
                                                                                                                                32 578.3 52.25 572.6 72.66L518.6 264.7C514.7 278.5 502.1 288 487.8 
                                                                                                                                288H170.7L179.9 336H488C501.3 336 512 346.7 512 360C512 373.3 501.3 
                                                                                                                                384 488 384H159.1C148.5 384 138.6 375.8 136.4 364.5L76.14 48H24C10.75 
                                                                                                                                48 0 37.25 0 24C0 10.75 10.75 0 24 0H96zM128 464C128 437.5 149.5 416 176 
                                                                                                                                416C202.5 416 224 437.5 224 464C224 490.5 202.5 512 176 512C149.5 512 128 
                                                                                                                                490.5 128 464zM512 464C512 490.5 490.5 512 464 512C437.5 512 416 490.5 416 
                                                                                                                                464C416 437.5 437.5 416 464 416C490.5 416 512 437.5 512 464z" />
                                                                                                                                </svg>
                                                                                                                            </span>
                                                                                                                        </li>
                                                                                                                    </ul>
                                                                                                                </div>
                                                                                                            </td>
                                                                                                        </tr>
                                                                                                    ))}

                                                                                                </tbody>
                                                                                            </table>
                                                                                        </InfiniteScroll>
                                                                            }
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div className="tab-pane sort-layout-single">
                                                            <div className="row">
                                                                <div className="col-12">
                                                                    <div className="product-list-single border-around">
                                                                        <a href="product-details-default.html" className="product-list-img-link">
                                                                            <img src="assets/images/products_images/aments_products_image_5.jpg" alt="" className="img-fluid" />
                                                                        </a>
                                                                        <div className="product-list-content">
                                                                            <h5 className="product-list-link"><a href="product-details-default.html">New Balance Fresh Foam Kaymin Car Purts</a></h5>
                                                                            <span className="product-list-price"><del className="product-list-price-off">$30.12</del> $25.12</span>
                                                                            <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Nobis ad, iure incidunt. Ab consequatur temporibus non eveniet inventore doloremque necessitatibus sed, ducimus quisquam, ad asperiores</p>
                                                                            <div className="product-action-icon-link-list">
                                                                                <ul>
                                                                                    <li><a href="wishlist.html"><i className="icon-heart"></i></a></li>
                                                                                    <li><a href="compare.html"><i className="icon-repeat"></i></a></li>
                                                                                    <li><a href="#" data-toggle="modal" data-target="#modalQuickview"><i className="icon-eye"></i></a></li>
                                                                                    <li><a href="#" data-toggle="modal" data-target="#modalAddcart"><i className="icon-shopping-cart"></i></a></li>
                                                                                </ul>
                                                                            </div>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <CartModal
                        cartItems={cartItems}
                        isModal={isModal}
                        setIsModal={setIsModal}
                    />
                </>
            }
        </>

    );
}

export default Shop;