import React from "react";

const CheckoutFizik = ({ handleChangeFizik, valuesFizik }) => {
    return (
        <div className="row">
            <div className="col-lg-6 mb-20">
                <div className="default-form-box">
                    <label>  <span>*</span></label>
                    <input
                        type="text"
                        value={valuesFizik.name}
                        onChange={handleChangeFizik('name')}
                        required
                    />
                </div>
            </div>
            <div className="col-lg-6 mb-20">
                <div className="default-form-box">
                    <label><span>*</span></label>
                    <input type="number"
                        value={valuesFizik.phone}
                        onChange={handleChangeFizik('phone')}
                        required
                    />
                </div>
            </div>
            <div className="col-lg-12 mb-20">
                <div className="default-form-box">
                    <label> Email  <span>*</span></label>
                    <input type="email"
                        value={valuesFizik.email}
                        onChange={handleChangeFizik('email')}
                        required
                    />
                </div>
            </div>
            <div className="col-12">
                <div className="order-notes">
                    <label htmlFor="order_note">  </label>
                    <textarea id="order_note" placeholder="   , ,   ."
                        value={valuesFizik.note}
                        onChange={handleChangeFizik('note')}
                        required></textarea>
                </div>
            </div>
        </div>
    );
}

export default CheckoutFizik;