import React, { useState, useEffect } from 'react';
import './styles/style.scss';
import Header from "./components/Header";
import Main from "./components/pages/Main/Main";
import Footer from "./components/Footer";
import Cart from "./components/pages/Cart/Cart";
import Error from "./components/pages/Error";
import { useFetching } from "./hooks/useFetching";
import { Routes, Route, useLocation } from 'react-router-dom';
import { Toaster, toast } from "react-hot-toast";
import { QueryClient, QueryClientProvider } from "react-query";
import ScrollToTopButton from './components/ScrollToTopButton';
import Wishlist from './components/pages/Wishlist/Wishlist';
import Shop from './components/pages/Shop/Shop';
import GetCategories from './API/GetCategories';
import GetSubCategories from './API/GetSubCategories';
import ShopItem from './components/pages/ShopItem/ShopItem';
import GetNews from './API/GetNews';
import GetServices from './API/GetServices';
import Blog from './components/pages/Blog/Blog';
import About from './components/pages/About/About';
import Contacts from './components/pages/Contacts/Contacts';
import Checkout from './components/pages/Checkout/Checkout';
import axios from 'axios';
import CookieConsent from "react-cookie-consent";
import BlogItem from './components/pages/Blog/BlogItem';
import Privacy from './components/pages/Privacy/Privacy';
import Delivery from './components/pages/Delivery/Delivery';
import Services from './components/pages/Services/Services';
import ServicesItem from './components/pages/Services/ServicesItem';
import ShopItemModal from './components/pages/ShopItem/ShopItemModal';

const queryClient = new QueryClient();
const App = () => {
    const [categories, setCategories] = useState([]);
    const [subCategories, setSubCategories] = useState([]);
    const [isModal, setIsModal] = useState(false);
    const [categoriesItems, setCategoriesItems] = useState([]);
    const [cartItems, setCartItems] = useState([]);
    const [wishlistItems, setWishlistItems] = useState([]);
    const [news, setNews] = useState([]);
    const [links, setLinks] = useState([]);
    const [hidden, setHidden] = useState(false);
    const [dropdown, setDropdown] = useState(false);
    const [cookieHidden, setCookieHidden] = useState(false);
    const location = useLocation();
    const pathname = window.location.pathname;
    const [pricelistLinks, setPricelistLinks] = useState([]);
    const [services, setServices] = useState([]);
    const [productModal, setProductModal] = useState(false);
    const [productModalId, setProductModalId] = useState(0);
    const [choosedCat, setChoosedCat] = useState(0);

    const [fetchCategories, isCategoriesLoading, categoriesError] = useFetching(async () => {
        let response = await GetCategories.getAllCategories();
        if (localStorage.getItem('categories') !== null) {
            setCategories(JSON.parse(localStorage.getItem('categories')));
        } else {
            setCategories(response.data);
        }
    });

    const [fetchSubCategories, isSubCategoriesLoading, subCategoriesError] = useFetching(async () => {
        let response = await GetSubCategories.getAllSubCategories();
        setSubCategories(response.data);
    });

    const [fetchNews, isNewsLoading, newsError] = useFetching(async () => {
        let response = await GetNews.getNews();
        setNews(response);
    });

    const [fetchServices, isServicesLoading, servicesError] = useFetching(async () => {
        let response = await GetServices.getServices();
        setServices(response);
    });

    const handleAddProduct = (product) => {
        const ProductExist = cartItems.find((item) => item.ID === product.ID);
        if (ProductExist) {
            localStorage.setItem('cartItems', JSON.stringify(cartItems.map((item) => item.ID === product.ID ?
                { ...ProductExist, quantity: ProductExist.quantity } : item)))
            setCartItems(JSON.parse(localStorage.getItem('cartItems')))
            toast.error('      ')

        } else {
            localStorage.setItem('cartItems', JSON.stringify([...cartItems, { ...product, quantity: 1 }]));
            setCartItems(JSON.parse(localStorage.getItem('cartItems')));
            setIsModal(true);

            window.dataLayer.push({
                "ecommerce": {
                    "currencyCode": "RUB",
                    "add": {
                        "products": [
                            {
                                "title": product.title,
                                "vendor_code": product.vendor_code,
                                "category": categories.filter(item => item.ID === product.category)[0].title,
                                "spare_parts_unit": subCategories.filter(item => item.ID === product.spare_parts_unit)[0].title,
                                "price": product.price + ' .'
                            }
                        ]
                    }
                }
            });
        }
    }

    const handleRemoveProduct = (product) => {
        localStorage.setItem('cartItems', JSON.stringify(cartItems.filter((item) => item.ID !== product.ID)));
        setCartItems(JSON.parse(localStorage.getItem('cartItems')));
    }

    const handleAddProductWishlist = (product) => {
        const ProductExist = wishlistItems.find((item) => item.ID === product.ID);
        if (ProductExist) {
            localStorage.setItem('wishlistItems', JSON.stringify(wishlistItems.map((item) => item.ID === product.ID ?
                { ...ProductExist, quantity: ProductExist.quantity } : item)))
            setWishlistItems(JSON.parse(localStorage.getItem('wishlistItems')));
            toast.error('       ')
        } else {
            localStorage.setItem('wishlistItems', JSON.stringify([...wishlistItems, { ...product, quantity: 1 }]))
            categories.map((item) => {
                if (item.ID === product.category) {
                    item.countitems += 1;
                }
            })
            localStorage.setItem('categories', JSON.stringify([...categories]))
            setCategories(JSON.parse(localStorage.getItem('categories')));
            setWishlistItems(JSON.parse(localStorage.getItem('wishlistItems')));
            toast.success('   ');
        }
    }

    const handleRemoveProductWishlist = (product) => {
        const ProductExist = wishlistItems.find((item) => item.ID === product.ID);
        if (ProductExist.quantity === 1) {
            localStorage.setItem('wishlistItems', JSON.stringify(wishlistItems.filter((item) => item.ID !== product.ID)))
            const categoriesWishlist = JSON.parse(localStorage.getItem('categories'));
            categoriesWishlist.map((item) => {
                if (item.ID === product.category) {
                    item.countitems -= 1;
                }
            })
            localStorage.setItem('categories', JSON.stringify([...categoriesWishlist]));
            setCategories(JSON.parse(localStorage.getItem('categories')));
            setWishlistItems(JSON.parse(localStorage.getItem('wishlistItems')));
        } else {
            localStorage.setItem('wishlistItems', JSON.stringify(wishlistItems.map((item) => item.ID === product.ID ?
                { ...ProductExist, quantity: ProductExist.quantity - 1 } : item)))
            setWishlistItems(JSON.parse(localStorage.getItem('wishlistItems')))
        }
    }

    const handleAddProductShop = (product) => {
        const ProductExist = cartItems.find((item) => item.ID === product.ID);
        if (ProductExist) {
            localStorage.setItem('cartItems', JSON.stringify(cartItems.map((item) => item.ID === product.ID ?
                { ...ProductExist, quantity: product.quantity === "0" || product.quantity === 1 ? 1 : product.quantity } : item)))
            setCartItems(JSON.parse(localStorage.getItem('cartItems')))
            toast.success('     ')
            window.dataLayer.push({
                "ecommerce": {
                    "currencyCode": "RUB",
                    "add": {
                        "products": [
                            {
                                "title": product.title,
                                "vendor_code": product.vendor_code,
                                "category": categories.filter(item => item.ID === product.category)[0].title,
                                "spare_parts_unit": subCategories.filter(item => item.ID === product.spare_parts_unit)[0].title,
                                "price": product.price + ' .'
                            }
                        ]
                    }
                }
            });
        } else {
            localStorage.setItem('cartItems', JSON.stringify([...cartItems, { ...product, quantity: product.quantity === "0" ? 1 : product.quantity }]))
            setCartItems(JSON.parse(localStorage.getItem('cartItems')));
            setIsModal(true);
            window.dataLayer.push({
                "ecommerce": {
                    "currencyCode": "RUB",
                    "add": {
                        "products": [
                            {
                                "title": product.title,
                                "vendor_code": product.vendor_code,
                                "category": categories.filter(item => item.ID === product.category)[0].title,
                                "spare_parts_unit": subCategories.filter(item => item.ID === product.spare_parts_unit)[0].title,
                                "price": product.price + ' .'
                            }
                        ]
                    }
                }
            });
        }
    }

    const getLinks = async () => {
        await axios.get(`https://deshevle-tut.ru/cms/links`)
            .then(response => setLinks(response.data.data));
    }

    const getPriceListLinks = async () => {
        await axios.get(`https://deshevle-tut.ru/price_list/`)
            .then(response => setPricelistLinks(response.data.data));
    }


    useEffect(() => {
        fetchCategories();
        fetchSubCategories();
        fetchNews();
        fetchServices();
        setCartItems(JSON.parse(localStorage.getItem('cartItems')) === null ? [] : JSON.parse(localStorage.getItem('cartItems')));
        setWishlistItems(JSON.parse(localStorage.getItem('wishlistItems')) === null ? [] : JSON.parse(localStorage.getItem('wishlistItems')));
        setCookieHidden(JSON.parse(localStorage.getItem('cookieHidden')))
        getLinks();
        getPriceListLinks();
    }, []);

    useEffect(() => {
        setDropdown(false);
    }, [location])

    return (
        <div className="app">
            <QueryClientProvider client={queryClient}>
                <Header
                    cartItems={cartItems}
                    handleRemoveProduct={handleRemoveProduct}
                    wishlistItems={wishlistItems}
                    handleRemoveProductWishlist={handleRemoveProductWishlist}
                    dropdown={dropdown}
                    setDropdown={setDropdown}
                    location={location}
                    categories={categories}
                    links={links}
                    setChoosedCat={setChoosedCat}
                />
                <div className="wrapper">
                    <Routes>
                        <Route path='/' element={
                            <Main
                                handleAddProduct={handleAddProduct}
                                cartItems={cartItems}
                                handleAddProductWishlist={handleAddProductWishlist}
                                wishlistItems={wishlistItems}
                                isModal={isModal}
                                setIsModal={setIsModal}
                                categories={categories}
                                isCategoriesLoading={isCategoriesLoading}
                                categoriesError={categoriesError}
                                subCategories={subCategories}
                                isSubCategoriesLoading={isSubCategoriesLoading}
                                subCategoriesError={subCategoriesError}
                                news={news}
                                pathname={pathname}
                                links={links}
                                setProductModal={setProductModal}
                                setProductModalId={setProductModalId}
                            />} />
                        <Route path='/cart' element={
                            <Cart
                                cartItems={cartItems}
                                handleRemoveProduct={handleRemoveProduct}
                                handleAddProductShop={handleAddProductShop}
                            />}
                        />
                        <Route path='/wishlist' element={
                            <Wishlist
                                wishlistItems={wishlistItems}
                                cartItems={cartItems}
                                isModal={isModal}
                                setIsModal={setIsModal}
                                handleAddProduct={handleAddProduct}
                                handleRemoveProductWishlist={handleRemoveProductWishlist}
                                categories={categories}
                            />}
                        />
                        <Route path='/shop' element={
                            <Shop
                                categoriesItems={categoriesItems}
                                handleAddProductShop={handleAddProductShop}
                                cartItems={cartItems}
                                isModal={isModal}
                                setIsModal={setIsModal}
                                handleAddProductWishlist={handleAddProductWishlist}
                                categories={categories}
                                subCategories={subCategories}
                                setHidden={setHidden}
                                pricelistLinks={pricelistLinks}
                                setProductModalId={setProductModalId}
                                setProductModal={setProductModal}
                                choosedCat={choosedCat}
                            />}
                        />
                        <Route path='item/:id' element={
                            <ShopItem
                                handleAddProductShop={handleAddProductShop}
                                cartItems={cartItems}
                                isModal={isModal}
                                setIsModal={setIsModal}
                                handleAddProductWishlist={handleAddProductWishlist}
                                handleAddProduct={handleAddProduct}
                                pathname={pathname}
                                setProductModalId={setProductModalId}
                                setProductModal={setProductModal}
                            />}
                        />
                        <Route path='/blog'>
                            <Route path='' element={
                                <Blog
                                    news={news}
                                    isNewsLoading={isNewsLoading}
                                    newsError={newsError}
                                />}
                            />
                            <Route path='/blog/:newsId' element={
                                <BlogItem
                                    news={news}
                                    pathname={pathname}
                                />}
                            />
                        </Route>
                        <Route path='/services'>
                            <Route path='' element={
                                <Services
                                    isServicesLoading={isServicesLoading}
                                    servicesError={servicesError}
                                    services={services}
                                />}
                            />
                            <Route path='/services/:serviceId' element={
                                <ServicesItem
                                    services={services}
                                    pathname={pathname}
                                />}
                            />
                        </Route >
                        <Route path='/contacts' element={
                            <Contacts
                                links={links}
                            />}
                        />
                        <Route path='/about' element={
                            <About
                            />}
                        />
                        <Route path='/privacy' element={
                            <Privacy
                            />}
                        />
                        <Route path='/delivery' element={
                            <Delivery
                            />}
                        />
                        <Route path='/checkout' element={
                            <Checkout
                                cartItems={cartItems}
                            />}
                        />
                        <Route path='*' element={<Error />} />
                    </Routes>
                </div>
                <Footer
                    links={links}
                    categories={categories}
                    pricelistLinks={pricelistLinks}
                    setChoosedCat={setChoosedCat}
                />
                <ScrollToTopButton />
                <Toaster />
                {cookieHidden ? '' : <CookieConsent
                    debug={true}
                    location='bottom'
                    style={{ background: '#E9E9E9', color: '#000', width: 'calc(100% - 30px)', right: '15px', left: 'auto', maxWidth: '500px', margin: '15px 0', filter: 'drop-shadow(0 10px 8px rgb(0 0 0 / 0.04)) drop-shadow(0 4px 3px rgb(0 0 0 / 0.1))', display: 'flex', alignItems: 'center' }}
                    buttonText=''
                    buttonStyle={{ background: '#ea1c26', color: '#fff', fontSize: '20px', padding: '5px 30px' }}
                    expires={150}
                    onAccept={() => {
                        localStorage.setItem('cookieHidden', JSON.stringify(true))
                        setCookieHidden(true)
                    }}
                >
                         ,    .  (),     .
                </CookieConsent>}
                {productModal ? <ShopItemModal
                    links={links}
                    productModal={productModal}
                    setProductModal={setProductModal}
                    productModalId={productModalId}
                    handleAddProductWishlist={handleAddProductWishlist}
                    handleAddProductShop={handleAddProductShop}
                /> : ''}
            </QueryClientProvider>
        </div>
    )
}

export default App;
