import React, { useEffect, useState } from "react"
import { NavLink } from 'react-router-dom'
import { formatUrl, splitText } from '../../../helpers/formatters'
import axios from "axios"
import Loading from '../../Loading'
import { Helmet, HelmetProvider } from "react-helmet-async";


const ServicesItem = ({
    services,
    pathname
}) => {
    const [loading, setLoading] = useState(true)
    const [error, setError] = useState(false)
    const [servicesItem, setServicesItem] = useState([])
    let servicesId = splitText(pathname)
    let otherServices = services.filter(el => `${el.id}` !== servicesId).slice(0, 3)

    const getThisItem = async (id) => {
        setLoading(true)
        await axios.get(`https://deshevle-tut.ru/service_detail/?service_id=${id}`)
            .then(response => setServicesItem(response.data.data))
            .catch(() => setError(true))
        setLoading(false)
    }

    useEffect(() => {
        getThisItem(servicesId)
    }, [])

    useEffect(() => {
        getThisItem(servicesId)
    }, [pathname])

    return (
        <>
            <HelmetProvider>
                <Helmet>
                    <title>{`  | ${servicesItem.map(product => product.title)}`}</title>
                    <meta name="description"
                        content={`        , 
                        ,           " "  .`
                        }
                    />
                </Helmet>
            </HelmetProvider>
            <div className="breadcrumb-section">
                <div className="breadcrumb-wrapper">
                    <div className="container">
                        <div className="row">
                            <div className="col-12 d-flex justify-content-between justify-content-md-between  align-items-center flex-md-row flex-column">
                                <h3 className="breadcrumb-title"></h3>
                                <div className="breadcrumb-nav">
                                    <nav aria-label="breadcrumb">
                                        <ul>
                                            <li><NavLink to="/"></NavLink></li>
                                            <li className="active" aria-current="page"></li>
                                        </ul>
                                    </nav>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {loading ? <Loading /> : error ?
                <>
                    <div className="blog-single-section">
                        <div className="container">
                            <div className="row flex-column-reverse flex-lg-row">
                                <div className="col-12">
                                    <div className="blog-single-wrapper">
                                        <h4 className="post-title"> ,  </h4>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </> :
                <>
                    <div className="blog-single-section">
                        <div className="container">
                            <div className="row flex-column-reverse flex-lg-row">
                                <div className="col-lg-3">
                                    <div className="siderbar-section">

                                        <div className="sidebar-single-widget">
                                            <h6 className="sidebar-title"> </h6>
                                            <div className="sidebar-content">
                                                <div className="recent-post">
                                                    <ul>
                                                        {otherServices.map((item, key) => (
                                                            <li className="recent-post-list" key={key}>
                                                                <NavLink to={`/services/${item.id}`} className="post-image">
                                                                    <img src={formatUrl(item.image)} alt={item.title} />
                                                                </NavLink>
                                                                <div className="post-content">
                                                                    <NavLink className="post-link" to={`/services/${item.id}`}>{item.title}</NavLink>
                                                                </div>
                                                            </li>
                                                        ))}
                                                    </ul>
                                                </div>
                                            </div>
                                        </div>

                                    </div>
                                </div>
                                <div className="col-lg-9">
                                    {servicesItem.map((item, key) => (
                                        <div className="blog-single-wrapper" key={key}>
                                            <div className="blog-single-img">
                                                <img className="img-fluid" src={formatUrl(item.image)} alt={item.title} />
                                            </div>
                                            <h4 className="post-title">{item.title}</h4>
                                            <div className="para-content">
                                                <p>{item.body}</p>
                                            </div>
                                        </div>
                                    ))}
                                </div>
                            </div>
                        </div>
                    </div>
                </>
            }
        </>
    )
}

export default ServicesItem