import React from "react";

const CheckoutJurik = ({ handleChangeJurik, valuesJurik }) => {
    return (
        <div className="row">
            <div className="col-lg-6 mb-20">
                <div className="default-form-box">
                    <label>  <span>*</span></label>
                    <input
                        type="text"
                        value={valuesJurik.company_name}
                        onChange={handleChangeJurik('company_name')}
                        required
                    />
                </div>
            </div>
            <div className="col-lg-6 mb-20">
                <div className="default-form-box">
                    <label> <span>*</span></label>
                    <input
                        type="number"
                        value={valuesJurik.inn}
                        onChange={handleChangeJurik('inn')}
                        required
                    />
                </div>
            </div>
            <div className="col-lg-6 mb-20">
                <div className="default-form-box">
                    <label>  <span>*</span></label>
                    <input
                        type="text"
                        value={valuesJurik.manager_name}
                        onChange={handleChangeJurik('manager_name')}
                        required
                    />
                </div>
            </div>
            <div className="col-lg-6 mb-20">
                <div className="default-form-box">
                    <label>  <span>*</span></label>
                    <input
                        type="number"
                        value={valuesJurik.manager_phone}
                        onChange={handleChangeJurik('manager_phone')}
                        required
                    />
                </div>
            </div>
            <div className="col-lg-12 mb-20">
                <div className="default-form-box">
                    <label>Email  <span>*</span></label>
                    <input
                        type="email"
                        value={valuesJurik.email}
                        onChange={handleChangeJurik('email')}
                        required
                    />
                </div>
            </div>
            <div className="col-12">
                <div className="order-notes">
                    <label htmlFor="order_note">  </label>
                    <textarea id="order_note" placeholder="   , ,   ."
                        value={valuesJurik.note}
                        onChange={handleChangeJurik('note')}
                    ></textarea>
                </div>
            </div>
        </div>
    );
}

export default CheckoutJurik;