import React from "react"
import { Swiper, SwiperSlide } from 'swiper/react'
import { Navigation, Lazy } from 'swiper'
import { formatUrl, formatDate } from '../../../helpers/formatters'
import { NavLink } from 'react-router-dom'

const MainBlog = ({ news }) => {
  console.log(news);
    return (
        <div className="blog-feed-section section-top-gap-100">
            <div className="section-content-gap">
                <div className="container">
                    <div className="row">
                        <div className="section-content">
                            <h3 className="section-title"> </h3>
                        </div>
                    </div>
                </div>
            </div>

            <div className="blog-feed-wrapper">
                <div className="container">
                    <div className="row">
                        {
                          news.length === 0
                          ?
                          ""
                          :
                          <Swiper
                            modules={[Navigation, Lazy]}
                            spaceBetween={15}
                            navigation
                            lazy={true}
                            autoHeight={true}
                            breakpoints={{
                                320: {
                                    slidesPerView: 1
                                },
                                576: {
                                    slidesPerView: 2
                                },
                                992: {
                                    slidesPerView: 3
                                }
                            }}>
                            {news.map((item, key) => (
                                  <SwiperSlide className="col-lg-4 col-md-6 col-12" key={key}>
                                      <div className="blog-feed-single">
                                          <NavLink to={`/blog/${item.id}`} className="blog-feed-img-link">
                                              <img src={formatUrl(item.image)} alt={item.title}
                                                  className="blog-feed-img" />
                                          </NavLink>
                                          <div className="blog-feed-content">
                                              <div className="blog-feed-post-meta">
                                                  <NavLink to={`/blog/${item.id}`} className="blog-feed-post-meta-date">{formatDate(item.created_at)}</NavLink>
                                              </div>
                                              <h5 className="blog-feed-link"><NavLink to={`/blog/${item.id}`}>{item.title}</NavLink></h5>
                                          </div>
                                      </div>
                                  </SwiperSlide>
                              ))}
                          </Swiper>
                        }
                    </div>
                </div>
            </div>


        </div>
    );
}

export default MainBlog;