import React from "react";
import { useState } from "react";

const ShopItemSpecif = ({ item }) => {
    console.log();
    return (
        <div className="tab-pane active show" id="specification">
            <div className="single-tab-content-item">
                <table className="table table-bordered mb-20">
                    <tbody>
                        {item.data[0].features.map((product, key) => (
                            <tr key={key}>
                                <th scope="row">{product.name_feature}</th>
                                <td>{product.value} {product.unit}</td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    );
}

export default ShopItemSpecif;