import React from "react";
import { Helmet, HelmetProvider } from "react-helmet-async";
import { NavLink } from 'react-router-dom';
import { formatUrl } from '../../../helpers/formatters';
import Loading from '../../Loading';

const Services = ({ services, isServicesLoading, servicesError }) => {
    return (
        <>
            <HelmetProvider>
                <Helmet>
                    <title>  | </title>
                    <meta name="description"
                        content='        , ,           " "  .' />
                </Helmet>
            </HelmetProvider>
            {isServicesLoading ? <Loading /> :
                servicesError ? <>
                    <div className="breadcrumb-section">
                        <div className="breadcrumb-wrapper">
                            <div className="container">
                                <div className="row">
                                    <div className="col-12 d-flex justify-content-between justify-content-md-between  align-items-center flex-md-row flex-column">
                                        <h3 className="breadcrumb-title"></h3>
                                        <div className="breadcrumb-nav">
                                            <nav aria-label="breadcrumb">
                                                <ul>
                                                    <li><NavLink to="/"></NavLink></li>
                                                    <li className="active" aria-current="page"></li>
                                                </ul>
                                            </nav>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="blog-section">
                        <div className="container">
                            <div className="row ">
                                <div className="col-12">
                                    <div className="blog-full-width-wrapper">
                                        <div className="row">
                                            <div className="col-12" style={{ fontSize: 26 }}>
                                                 , ,  
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </> :
                    <>
                        <div className="breadcrumb-section">
                            <div className="breadcrumb-wrapper">
                                <div className="container">
                                    <div className="row">
                                        <div className="col-12 d-flex justify-content-between justify-content-md-between  align-items-center flex-md-row flex-column">
                                            <h3 className="breadcrumb-title"></h3>
                                            <div className="breadcrumb-nav">
                                                <nav aria-label="breadcrumb">
                                                    <ul>
                                                        <li><NavLink to="/"></NavLink></li>
                                                        <li className="active" aria-current="page"></li>
                                                    </ul>
                                                </nav>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="blog-section">
                            <div className="container">
                                <div className="row ">
                                    <div className="col-12">
                                        <div className="blog-full-width-wrapper">
                                            <div className="row">
                                                {services.map((item, key) => (
                                                    <div className="col-lg-4 col-md-6 col-12" key={key}>
                                                        <div className="blog-feed-single">
                                                            <NavLink to={`/services/${item.id}`} className="blog-feed-img-link">
                                                                <img src={formatUrl(item.image)} alt={item.title} className="blog-feed-img" />
                                                            </NavLink>
                                                            <div className="blog-feed-content">
                                                                <h5 className="blog-feed-link"><NavLink to={`/services/${item.id}`}>{item.title}</NavLink></h5>
                                                            </div>
                                                        </div>
                                                    </div>
                                                ))}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </>
            }
        </>
    );
}

export default Services;