import React, { useEffect, useState } from "react";
import Loading from '../../Loading';
import axios from "axios";
import { toast } from 'react-hot-toast';
import ReCAPTCHA from "react-google-recaptcha";


const ShopItemReviews = ({ item, feedback, loading, getFeedback }) => {
    const [checkedCaptcha, setCheckedCaptcha] = useState(false)
    const recaptchaRef = React.createRef();
    const [values, setValues] = useState({
        name: '',
        email: '',
        body: '',
        product_id: +item.data[0].ID
    })

    const handleSubmit = async (e) => {
        e.preventDefault();

        const { name, email, body, product_id } = values;
        const user = { name, email, body, product_id };

        if (checkedCaptcha) {
            try {
                await axios.post(`https://deshevle-tut.ru/feedback/`, user)
                    .then(() => { toast.success('   !') })
                    .catch(() => { toast.error('    , ,  ') });
            } catch {
                toast.error(' , ,  !')
            }
        } else {
            toast.error(', ,    !')
        }

        getFeedback();
    };

    const handleChange = name => e => {
        setValues({ ...values, [name]: e.target.value });
    };

    useEffect(() => {
        getFeedback();
    }, [item])

    return (
        <>
            {loading ? <Loading /> :
                <div className="tab-pane active show" id="review">
                    <div className="single-tab-content-item">
                        <ul className="comment">
                            {feedback.length === 0 ? <strong style={{ fontSize: 22, textAlign: 'center', width: '100%', display: 'inline-block', borderBottom: '1px solid #ddd', paddingBottom: '40px' }}>  !</strong> :
                                feedback.map((item, key) => (
                                    <li className="comment-list" key={key} style={{ borderBottom: '1px solid #ddd', paddingBottom: '40px' }}>
                                        <div className="comment-wrapper">
                                            <div className="comment-content">
                                                <div className="comment-content-top">
                                                    <div className="comment-content-left">
                                                        <h6 className="comment-name">{item.name}</h6>
                                                    </div>
                                                </div>

                                                <div className="para-content">
                                                    <p>{item.body}</p>
                                                </div>
                                            </div>
                                        </div>
                                    </li>
                                ))
                            }

                        </ul>
                        <div className="review-form">
                            <div className="review-form-text-top">
                                <h5> </h5>
                                <p>      .    *</p>
                            </div>

                            <form onSubmit={handleSubmit}>
                                <div className="row">
                                    <div className="col-md-6">
                                        <div className="default-form-box mb-20">
                                            <label htmlFor="comment-name">  <span>*</span></label>
                                            <input
                                                id="comment-name"
                                                value={values.name}
                                                onChange={handleChange('name')}
                                                type="text"
                                                placeholder=" "
                                                required />
                                        </div>
                                    </div>
                                    <div className="col-md-6">
                                        <div className="default-form-box mb-20">
                                            <label htmlFor="comment-email"> Email <span>*</span></label>
                                            <input
                                                id="comment-email"
                                                value={values.email}
                                                onChange={handleChange('email')}
                                                type="email"
                                                placeholder=" email"
                                                required />
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <div className="default-form-box mb-20">
                                            <label htmlFor="comment-review-text">  <span>*</span></label>
                                            <textarea
                                                id="comment-review-text"
                                                value={values.body}
                                                onChange={handleChange('body')}
                                                placeholder=" "
                                                required></textarea>
                                        </div>
                                    </div>
                                    <div className="col-12">
                                        <button className="form-submit-btn" type="submit"></button>
                                    </div>
                                    <div className="col-12">
                                        <ReCAPTCHA className="pt-15"
                                            ref={recaptchaRef}
                                            sitekey={process.env.REACT_APP_RECAPTCHA_KEY}
                                            onChange={() => { setCheckedCaptcha(true) }}
                                        />
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            }
        </>
    );
}

export default ShopItemReviews;