import axios from "axios";
import React, { useState } from "react";
import { useEffect } from "react";
import toast from "react-hot-toast";
import Loading from "./Loading";

const BannerSection = () => { 
    const [banner, setBanner] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
    const getBanner = async () => {
      await axios.get('https://deshevle-tut.ru/cms/label')
        .then((response) => {
          setBanner(response.data.data);
          setIsLoading(false);
        })
        .catch(() => {
          toast.error('     ,   !')
          setIsLoading(false);
        })
    }

    useEffect(() => {
      getBanner();
    }, [])
    return (
        <div className="banner-section section-top-gap-100">
            <div className="banner-wrapper">
                <div className="container">
                    <div className="row">
                        <div className="col-12">
                            {
                              isLoading 
                              ? 
                              <Loading/> 
                              :
                              banner.map((item, key) => (
                                <div className="banner-single" key={key}>
                                    <a href="#" className="banner-img-link">
                                        <img className="banner-img banner-img-big"
                                            src={item.image} alt="bannerImg" />
                                    </a>
                                    <div className="banner-content">
                                        <span className="banner-text-small">{item.upper_text}</span>
                                        <h2 className="banner-text-big"><span className="banner-text-big-highlight">{item.marked_text}</span> {item.main_text}</h2>
                                    </div>
                                </div>
                              ))
                            }
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default BannerSection;