import React, { useEffect, useState } from "react";
import { Pagination } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import { NavLink } from 'react-router-dom';
import 'swiper/css';
import 'swiper/css/pagination';
import axios from "axios";
import Loading from '../../Loading';

const MainSlider = () => {
    const [slides, setSlides] = useState([]);
    const [loading, setLoading] = useState(true);
    useEffect(() => {
        const getSlides = async () => {
            setLoading(true);
            await axios.get(`https://deshevle-tut.ru/cms/sliders`)
                .then(response => setSlides(response.data.data));
            setLoading(false);
        }
        getSlides();
    }, [])
    return (
        <>
            {loading ? <Loading /> : <div className="hero-area">
                <div className="hero-area-wrapper">
                    <Swiper
                        modules={[Pagination]}
                        slidesPerView={1}
                        pagination={{ clickable: true }}
                        loop={true}>
                        {slides.length === 0 ? <SwiperSlide>
                            <div className="hero-area-single">
                                <div className="hero-area-bg">
                                    <img className="hero-img"
                                        alt="mainSliderImg1" />
                                </div>
                                <div className="hero-content">
                                    <div className="container">
                                        <div className="row">
                                            <div className="col-10 col-md-8 col-xl-6 main-text">
                                                <h5>   </h5>
                                                <h2> </h2>
                                                <p>,        .</p>
                                                <NavLink to="/shop" className="hero-button">  </NavLink>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </SwiperSlide> : slides.map((item, key) => (
                            <SwiperSlide key={key}>
                                <div className="hero-area-single">
                                    <div className="hero-area-bg">
                                        <img className="hero-img" src={item.image}
                                            alt={item.upper_text} />
                                    </div>
                                    <div className="hero-content">
                                        <div className="container">
                                            <div className="row">
                                                <div className="col-12 main-text">
                                                    <h5>{item.upper_text}</h5>
                                                    <h2>{item.main_text}</h2>
                                                    <p>{item.down_text}</p>
                                                    <NavLink to="/shop" className="hero-button">  </NavLink>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </SwiperSlide>
                        ))}
                    </Swiper>
                </div>
            </div>
            }
        </>
    );
}

export default MainSlider;