import React, { useEffect, useState } from "react";
import { NavLink } from 'react-router-dom';
import Error from '../Error';
import { formatUrl, splitText } from '../../../helpers/formatters';
import Loading from '../../Loading';
import CartModal from '../Cart/CartModal';
import ShopItemTabs from "./ShopItemTabs";
import ShopItemRelated from "./ShopItemRelated";
import axios from "axios";
import { Helmet, HelmetProvider } from "react-helmet-async";

const ShopItem = ({
    handleAddProductShop,
    cartItems,
    isModal,
    setIsModal,
    handleAddProductWishlist,
    handleAddProduct,
    pathname,
    setProductModalId,
    setProductModal
}) => {
    const [loading, setLoading] = useState(true)
    const [error, setError] = useState(false)
    const [item, setItem] = useState({
        data: [],
        relatives: []
    })

    const scrollToTop = () => {
        window.scrollTo({
            top: 0,
            behavior: "smooth"
        })
    }

    const getThisItem = async (id) => {
        setLoading(true)
        await axios.get(`https://deshevle-tut.ru/product_detail?prod_id=${id}`)
            .then(response => setItem(response.data))
            .catch(() => { setError(true) })
        setLoading(false)
    }

    useEffect(() => {
        getThisItem(splitText(pathname))
    }, [])

    useEffect(() => {
        getThisItem(splitText(pathname))
    }, [pathname])

    return (
        <>
            <HelmetProvider>
                <Helmet>
                    <title>{`  | ${item.data.map(product => product.title)}`}</title>
                    <meta name="description"
                        content={item.data.map(product => product.description === '0' || product.description === '' ?
                            `        , 
                            ,           " "  .`
                            :
                            product.description
                        )}
                    />
                </Helmet>
            </HelmetProvider>
            {loading ? <Loading /> : error ? <Error /> :
                item.data.length === 0 ? '' :
                    <>
                        {item.data.map((product, key) => (
                            <div key={key}>
                                <div className="breadcrumb-section">
                                    <div className="breadcrumb-wrapper">
                                        <div className="container">
                                            <div className="row">
                                                <div className="col-12 d-flex justify-content-between justify-content-md-between  align-items-center flex-md-row flex-column">
                                                    <h3 className="breadcrumb-title product-details-title-breadcumb">{product.title}</h3>
                                                    <div className="breadcrumb-nav">
                                                        <nav aria-label="breadcrumb">
                                                            <ul>
                                                                <li><NavLink to="/"></NavLink></li>
                                                                <li><NavLink to="/shop"></NavLink></li>
                                                                <li className="active" aria-current="page"></li>
                                                            </ul>
                                                        </nav>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="product-details-section">
                                    <div className="container">
                                        <div className="row">
                                            <div className="col-md-6">
                                                <div className="product-details-gallery-area product-details-img-sticky">
                                                    <div className="product-large-image product-large-image-horaizontal">
                                                        <div className="product-image-large-single zoom-image-hover">
                                                            <img className={product.image_original === 'https://deshevle-tut.ru/data/photos/0' || product.image_432 === '' || product.image_432 === 'https://deshevle-tut.ru/data/photos/432/432__--.jpg' ? 'none-image none-image-item' : ''} src={product.image_original === 'https://deshevle-tut.ru/data/photos/0' || product.image_original === '' || product.image_original === 'https://deshevle-tut.ru/data/photos/original/--.jpg' ? 'https://deshevle-tut.ru/data/photos/original/--.jpg' : formatUrl(product.image_original)} alt={product.title} />
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="col-md-6">
                                                <div className="product-details-content-area">
                                                    <div className="product-details-text">
                                                        <h4 className="title mb-1">{product.title}</h4>
                                                        <div className="product-details-vendor">( {product.vendor_code})</div>
                                                        <div className="price">{product.price === 0 ? '' : product.price + ' . (   )'}</div>
                                                        <p>{product.short_description === '0' || product.short_description === '' ? '' : product.short_description === null ? product.description : product.short_description}</p>
                                                    </div>
                                                    <div className="product-details-variable">
                                                        <h4 className="title"> </h4>
                                                        <div className="d-flex align-items-center">
                                                            <div className="variable-single-item ">
                                                                <span></span>
                                                                <div className="product-variable-quantity">
                                                                    <input className="product-count" type="number" min="1" max="100000" placeholder="1"
                                                                        onChange={(e) => {
                                                                            product.quantity = e.target.value;
                                                                        }}
                                                                    />
                                                                </div>
                                                            </div>

                                                            <div className="product-add-to-cart-btn">
                                                                <span onClick={() => { handleAddProductShop(product) }}>  </span>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div className="product-details-meta mb-20">
                                                        <ul>
                                                            <li>
                                                                <span className="item-to-wishlist" onClick={() => { handleAddProductWishlist(product) }}>
                                                                    <svg className="svg-item-icon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><path d="M244 84L255.1 96L267.1 84.02C300.6 51.37 347 36.51 392.6 44.1C461.5 55.58 512 115.2 512 185.1V190.9C512 232.4 494.8 272.1 464.4 300.4L283.7 469.1C276.2 476.1 266.3 480 256 480C245.7 480 235.8 476.1 228.3 469.1L47.59 300.4C17.23 272.1 0 232.4 0 190.9V185.1C0 115.2 50.52 55.58 119.4 44.1C164.1 36.51 211.4 51.37 244 84C243.1 84 244 84.01 244 84L244 84zM255.1 163.9L210.1 117.1C188.4 96.28 157.6 86.4 127.3 91.44C81.55 99.07 48 138.7 48 185.1V190.9C48 219.1 59.71 246.1 80.34 265.3L256 429.3L431.7 265.3C452.3 246.1 464 219.1 464 190.9V185.1C464 138.7 430.4 99.07 384.7 91.44C354.4 86.4 323.6 96.28 301.9 117.1L255.1 163.9z" /></svg>
                                                                      
                                                                </span>
                                                            </li>
                                                        </ul>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        ))}
                        <ShopItemTabs
                            item={item}
                        />
                        <ShopItemRelated
                            item={item}
                            handleAddProduct={handleAddProduct}
                            handleAddProductWishlist={handleAddProductWishlist}
                            scrollToTop={scrollToTop}
                            pathname={pathname}
                            setProductModalId={setProductModalId}
                            setProductModal={setProductModal}
                        />
                    </>
            }
            <CartModal cartItems={cartItems} isModal={isModal} setIsModal={setIsModal} />
        </>
    );
}

export default ShopItem;