import React from "react";
import { Helmet, HelmetProvider } from "react-helmet-async";
import { NavLink } from 'react-router-dom';
import { formatUrl } from '../../../helpers/formatters';
import CartModal from '../Cart/CartModal'

const Wishlist = ({
    wishlistItems,
    cartItems,
    isModal,
    setIsModal,
    handleAddProduct,
    handleRemoveProductWishlist,
}) => {
    const categories = JSON.parse(localStorage.getItem('categories'));
    return (
        <>
            <HelmetProvider>
                <Helmet>
                    <title>  | </title>
                    <meta name="description"
                        content='        , ,           " "  .' />
                </Helmet>
            </HelmetProvider>
            <div className="breadcrumb-section">
                <div className="breadcrumb-wrapper">
                    <div className="container">
                        <div className="row">
                            <div className="col-12 d-flex justify-content-between justify-content-md-between  align-items-center flex-md-row flex-column">
                                <h3 className="breadcrumb-title"></h3>
                                <div className="breadcrumb-nav">
                                    <nav aria-label="breadcrumb">
                                        <ul>
                                            <li><NavLink to="/"></NavLink></li>
                                            <li><NavLink to="/shop"></NavLink></li>
                                            <li className="active" aria-current="page"></li>
                                        </ul>
                                    </nav>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="wishlist-section">
                <div className="wishlish-table-wrapper">
                    <div className="container">
                        <div className="row">
                            <div className="col-12">
                                <div className="">
                                    <div className="table_page table-responsive">
                                        {wishlistItems.length === 0 ? <span className="none-cart">     </span> :
                                            <>
                                                {categories.map((productItem, key) => (
                                                    productItem.countitems <= 0 ? '' :
                                                        <div key={key} style={{ marginBottom: 50 }}>
                                                            <h2 style={{ textAlign: 'center', marginBottom: 15, marginTop: 15 }}>{productItem.title}</h2>
                                                            <div className="table_desc">
                                                                <table>
                                                                    <thead>
                                                                        <tr>
                                                                            <th className="product_remove"></th>
                                                                            <th className="product_thumb"></th>
                                                                            <th className="product_name"></th>
                                                                            <th className="product_articule"></th>
                                                                            <th className="product-price"></th>
                                                                            <th className="product_stock"></th>
                                                                            <th className="product_addcart">  </th>
                                                                        </tr>
                                                                    </thead>
                                                                    {wishlistItems.map((item, key) => (
                                                                        item.category === productItem.ID ?
                                                                            <tbody key={key}>
                                                                                <tr key={item.ID}>
                                                                                    <td className="product_remove"><span onClick={() => { handleRemoveProductWishlist(item) }}><i className="fa fa-trash-o"></i></span></td>
                                                                                    <td className="product_thumb"><NavLink to={`/item/${item.ID}`} ><img src={item.image_432 === 'https://deshevle-tut.ru/data/photos/0' || item.image_128 === '' || item.image_128 === 'https://deshevle-tut.ru/data/photos/128/128__--.jpg' ? 'https://deshevle-tut.ru/data/photos/128/128__--.jpg' : formatUrl(item.image_128)} alt={item.title} /></NavLink></td>
                                                                                    <td className="product_name"><NavLink to={`/item/${item.ID}`} >{item.title}</NavLink></td>
                                                                                    <td className="product_articule"><span>{item.vendor_code}</span></td>
                                                                                    <td className="product-price">{item.price === 0 ? '' : item.price + ' .'}</td>
                                                                                    <td className="product_stock"> </td>
                                                                                    <td className="product_addcart"><span onClick={() => { handleAddProduct(item); handleRemoveProductWishlist(item) }}></span></td>
                                                                                </tr>
                                                                            </tbody> :
                                                                            ''
                                                                    ))}
                                                                </table>
                                                            </div>
                                                        </div>
                                                ))}

                                            </>
                                        }
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <CartModal cartItems={cartItems} isModal={isModal} setIsModal={setIsModal} />
        </>
    );
}

export default Wishlist;