import React, { useState } from "react";
import { NavLink } from 'react-router-dom';


const CartModal = ({ cartItems, isModal, setIsModal }) => {
    const totalPrice = cartItems.reduce((price, item) => price + item.quantity * item.price, 0)
    return (
        <>
            {isModal ?
                <div className="modal fade show">
                    <div className="modal-dialog modal-dialog-centered modal-xl" role="document">
                        <div className="modal-content">
                            <div className="modal-body">
                                <div className="container-fluid">
                                    <div className="row">
                                        <div className="col text-right">
                                            <button type="button" className="close modal-close" onClick={() => { setIsModal(false) }}>
                                                <span aria-hidden="true"> <i className="fa fa-times"></i></span>
                                            </button>
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-md-7">
                                            <div className="row">
                                                <div className="col-md-8">
                                                    <div className="modal-add-cart-info"><i className="fa fa-check-square"></i>   !</div>
                                                    <div className="modal-add-cart-product-cart-buttons" onClick={() => { setIsModal(false) }}>
                                                        <NavLink to="/cart">  </NavLink>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="col-md-5 modal-border">
                                            <ul className="modal-add-cart-product-shipping-info">
                                                <li> <strong><i className="icon-shopping-cart"></i>   {cartItems.length} </strong></li>
                                                <li> <strong> : </strong> <span>{totalPrice} .</span></li>
                                                <li className="modal-continue-button"><span onClick={() => { setIsModal(false) }}> </span></li>
                                            </ul>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div> : ''}
        </>
    );
}

export default CartModal;