import React, { useEffect, useState } from "react";
import { Helmet, HelmetProvider } from "react-helmet-async";
import { NavLink } from 'react-router-dom';
import { formatUrl } from '../../../helpers/formatters';

const Cart = ({
    cartItems,
    handleRemoveProduct,
    handleAddProductShop,
}) => {
    const [totalPrice, setTotalPrice] = useState(cartItems.reduce((price, item) => price + item.quantity * item.price, 0));
    const [isPrice, setIsPrice] = useState(0);
    const [isMobile, setIsMobile] = useState(false);

    const handleResize = () => {
        if (window.innerWidth < 992) {
            setIsMobile(true)
        } else {
            setIsMobile(false)
        }
    }

    useEffect(() => {
        setTotalPrice(cartItems.reduce((price, item) => price + item.quantity * item.price, 0));
    }, [isPrice, cartItems]);

    useEffect(() => {
        window.addEventListener("resize", handleResize)
    })

    useEffect(() => {
        handleResize();
    }, []);

    return (
        <>
            <HelmetProvider>
                <Helmet>
                    <title>  | </title>
                    <meta name="description"
                        content='        , ,           " "  .' />
                </Helmet>
            </HelmetProvider>
            <div className="breadcrumb-section">
                <div className="breadcrumb-wrapper">
                    <div className="container">
                        <div className="row">
                            <div className="col-12 d-flex justify-content-between justify-content-md-between  align-items-center flex-md-row flex-column">
                                <h3 className="breadcrumb-title"></h3>
                                <div className="breadcrumb-nav">
                                    <nav aria-label="breadcrumb">
                                        <ul>
                                            <li><NavLink to="/"></NavLink></li>
                                            <li><NavLink to="/shop"></NavLink></li>
                                            <li className="active"></li>
                                        </ul>
                                    </nav>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="cart-section">
                <div className="cart-table-wrapper">
                    <div className="container">
                        <div className="row">
                            <div className="col-12">
                                <div className="table_desc">
                                    <div className="table_page table-responsive">
                                        {cartItems.length === 0 ?
                                            <>
                                                <span className="none-cart">  </span>
                                            </>
                                            :
                                            isMobile ?
                                                <table>
                                                    <thead>
                                                        <tr>
                                                            <th className="product_name"></th>
                                                            <th className="product-price"></th>
                                                            <th className="product_quantity">-</th>
                                                            <th className="product_total"></th>
                                                            <th className="product_remove"></th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        {cartItems.map((item) => (
                                                            <tr key={item.ID}>
                                                                <td className="product_name"><NavLink to={`/item/${item.ID}`} ><span style={{ fontWeight: 700, display: 'block' }}>{item.title}</span> (. {item.vendor_code})</NavLink></td>
                                                                <td className="product-price">{item.price === 0 ? '' : item.price + ' .'}</td>
                                                                <td className="product_quantity">
                                                                    <input className="product-count" type="number" min="1" max="100000" placeholder="1" value={item.quantity}
                                                                        onChange={(e) => {
                                                                            item.quantity = e.target.value;
                                                                            handleAddProductShop(item)
                                                                            setIsPrice(value => value + 1);
                                                                        }}
                                                                    />
                                                                </td>
                                                                <td className="product_total">{item.price === 0 ? '' : (item.price * item.quantity) + ' .'}</td>
                                                                <td className="product_remove" style={{ padding: '5px' }}><span onClick={() => { handleRemoveProduct(item) }}><i className="fa fa-trash-o"></i></span></td>
                                                            </tr>
                                                        ))}
                                                    </tbody>
                                                </table>
                                                :
                                                <table>
                                                    <thead>
                                                        <tr>
                                                            <th className="product_remove"></th>
                                                            <th className="product_thumb"></th>
                                                            <th className="product_name"></th>
                                                            <th className="product_articule"></th>
                                                            <th className="product-price"></th>
                                                            <th className="product_quantity"></th>
                                                            <th className="product_total"></th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        {cartItems.map((item) => (
                                                            <tr key={item.ID}>
                                                                <td className="product_remove"><span onClick={() => { handleRemoveProduct(item) }}><i className="fa fa-trash-o"></i></span></td>
                                                                <td className="product_thumb"><NavLink to={`/item/${item.ID}`}><img src={item.image_432 === 'https://deshevle-tut.ru/data/photos/0' || item.image_128 === '' || item.image_128 === 'https://deshevle-tut.ru/data/photos/128/128__--.jpg' ? 'https://deshevle-tut.ru/data/photos/128/128__--.jpg' : formatUrl(item.image_128)} alt={item.title} /></NavLink></td>
                                                                <td className="product_name"><NavLink to={`/item/${item.ID}`} >{item.title}</NavLink></td>
                                                                <td className="product_articule"><span>{item.vendor_code}</span></td>
                                                                <td className="product-price">{item.price === 0 ? '' : item.price + ' .'}</td>
                                                                <td className="product_quantity">
                                                                    <input className="product-count" type="number" min="1" max="100000" placeholder="1" value={item.quantity}
                                                                        onChange={(e) => {
                                                                            item.quantity = e.target.value;
                                                                            handleAddProductShop(item)
                                                                            setIsPrice(value => value + 1);
                                                                        }}
                                                                    />
                                                                </td>
                                                                <td className="product_total">{item.price === 0 ? '' : (item.price * item.quantity) + ' .'}</td>
                                                            </tr>
                                                        ))}
                                                    </tbody>
                                                </table>
                                        }
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                {cartItems.length === 0 ? '' :
                    <div className="coupon_area">
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-12 col-md-12">
                                    <div className="coupon_code right">
                                        <h3></h3>
                                        <div className="coupon_inner">
                                            <div className="cart_subtotal">
                                                <p>  </p>
                                                <p className="cart_amount">{totalPrice} .</p>
                                            </div>
                                            <div className="checkout_btn">
                                                {totalPrice === 0 ? <a href="javascript:void(0)" className="checkout_btn disabled">  </a> : <NavLink to="/checkout">  </NavLink>}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                }

            </div>
        </>
    );
}

export default Cart;