import React, { useState, useEffect } from "react"
import { NavLink } from "react-router-dom"
import { formatUrl } from '../../../helpers/formatters'
import axios from "axios"
import Loading from '../../Loading'
import { Helmet, HelmetProvider } from "react-helmet-async"

const About = () => {
    const [loading, setLoading] = useState(true)
    const [error, setError] = useState(false)
    const [aboutItems, setAboutItems] = useState([])

    const getAbout = async () => {
        setLoading(true)
        await axios.get(`https://deshevle-tut.ru/cms/about`)
            .then(response => setAboutItems(response.data.data))
            .catch(() => setError(true))
        setLoading(false)
    }

    useEffect(() => {
        getAbout()
    }, [])
    return (
        <>

            <HelmetProvider>
                <Helmet>
                    <title>  |  </title>
                    <meta name="description"
                        content='        , ,           " "  .' />
                </Helmet>
            </HelmetProvider>
            <div className="breadcrumb-section">
                <div className="breadcrumb-wrapper">
                    <div className="container">
                        <div className="row">
                            <div className="col-12 d-flex justify-content-between justify-content-md-between  align-items-center flex-md-row flex-column">
                                <h3 className="breadcrumb-title"> </h3>
                                <div className="breadcrumb-nav">
                                    <nav aria-label="breadcrumb">
                                        <ul>
                                            <li><NavLink to="/"></NavLink></li>
                                            <li className="active" aria-current="page"> </li>
                                        </ul>
                                    </nav>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {
                loading ? <Loading /> : error ?
                    <div className="history__content">
                        <div className="history__container container">
                            <div className="col-12">
                                 ,  
                            </div>
                        </div>
                    </div>
                    :
                    <div className="history__content">
                        <div className="history__container container">
                            {aboutItems.map((item, key) => (
                                <div className="history__item" key={key}>
                                    <div className="history__left">
                                        <div className="history__slide">
                                            <img src={formatUrl(item.image)} alt={item.id} />
                                        </div>
                                    </div>
                                    <div className="history__right">
                                        <p className="history__item-descr"><span>{item.main_text}</span></p>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
            }
        </>
    );
}

export default About;